export default class nscCore {
  static lastError() {
    if (chrome.runtime.lastError) {
      return chrome.runtime.lastError.message;
    }
    return null;
  }

  static async tabCreate(url, param) {
    param = {
      url,
      ...param
    };

    return chrome.tabs.create(param);
  }

  static async tabRemove(tabId) {
    await chrome.tabs.remove(tabId);
    const lastError = this.lastError();
    if (lastError) {
      console.error(lastError);
    }
  }

  static async windowCreate(url, param) {
    const screens = await chrome.system.display.getInfo();
    const screen = screens.find(s => s.isPrimary);
    const width = Math.ceil(param.width || screen.bounds.width / 2);
    const height = Math.ceil(param.height || screen.bounds.height / 2);
    const left = Math.ceil((screen.bounds.width - width) / 2);
    const top = Math.ceil((screen.bounds.height - height) / 2);

    param = {
      url,
      type: 'popup',
      left,
      top,
      width,
      height,
      ...param
    };
    console.log(JSON.stringify(param));
    return chrome.windows.create(param);
  }

  static async windowUpdate(windowId, param) {
    console.log('windowUpdate', windowId, param);
    await chrome.windows.update(windowId, param);
  }

  static async windowRemove(windowId) {
    await chrome.windows.remove(windowId);
    const lastError = this.lastError();
    if (lastError) {
      console.error(lastError);
    }
  }

  static async tabGet(tabId) {
    return new Promise(resolve => {
      try {
        chrome.tabs.get(tabId, resolve);
      } catch (e) {
        resolve(false);
      }
    });
  }

  static async removeAuthToken(token) {
    return new Promise(resolve => {
      if (!token) {
        resolve();
      } else {
        chrome.identity.removeCachedAuthToken({ token }, resolve);
      }
    });
  }

  static async getAuthToken(details) {
    return new Promise(resolve => {
      chrome.identity.getAuthToken(details, resolve);
    });
  }

  static async tabQuery(query = {}) {
    return new Promise(resolve => {
      chrome.tabs.query(query, resolve);
    });
  }

  static async tabActive(addSystemTab) {
    const [tab] = await this.tabQuery({ active: true, lastFocusedWindow: true });
    if (
      tab
      && (
        (
          !/^chrome/.test(tab.url)
          && !/chrome.google.com\/webstore/.test(tab.url)
        )
          || addSystemTab
      )
    ) {
      return tab;
    }
    return null;
  }

  static async tabAllActive() {
    const tabs = await this.tabQuery({});

    return tabs.reduce((accum, tab) => {
      if (!/^chrome/.test(tab.url)
        && !/chrome.google.com\/webstore/.test(tab.url)
        && tab.status !== 'unloaded'
      ) {
        return [...accum, tab];
      }
      return accum;
    }, []);
  }

  static async tabUpdate(tabId, properties) {
    chrome.tabs.update(tabId, properties);
  }

  static async sendTabMessage(tab, data) {
    if (!tab) {
      tab = await this.tabActive();
    }
    return chrome.tabs.sendMessage(tab.id, data);
  }

  static syncSendTabMessage(tab, data) {
    (async () => {
      if (!tab) {
        tab = await this.tabActive();
      }
      return chrome.tabs.sendMessage(tab.id, data);
    })();
  }

  static async sendAllTabMessage(data, notId) {
    const tabs = await this.tabQuery({});

    const promises = tabs.map(async tab => {
      if (tab.id !== notId) {
        return this.sendTabMessage(tab, data);
      }
      return null;
    });

    return Promise.all(promises);
  }

  static syncSendAllTabMessage(data, notId) {
    (async () => {
      const tabs = await this.tabQuery({});

      tabs.forEach(tab => {
        if (tab.id !== notId) {
          chrome.tabs.sendMessage(tab.id, data);
        }
      });
    })();
  }

  static async sendMessage(data) {
    return new Promise(resolve => {
      chrome.runtime.sendMessage(data, resolve);
    });
  }

  static sendMessageSync(data, cb) {
    try {
      chrome.runtime.sendMessage(data, cb);
      // eslint-disable-next-line no-empty
    } catch (e) {}
  }

  static async executeFiles(files, tab, allTabs) {
    if (!Array.isArray(files)) {
      files = [files];
    }

    let tabs = [];

    if (allTabs) {
      tabs = await this.tabAllActive();
    } else if (!tab) {
      tabs = [await this.tabActive()];
    } else {
      tabs = [tab];
    }

    //console.log('executeFiles', tabs, files);

    try {
      await Promise.all(
        files.map(async file => {
          if (/\.css$/.test(file)) {
            tabs.map(async ({ id }) => {
              await chrome.scripting.insertCSS({ target: { tabId: id }, files: [file] });
            });
          } else if (/\.js/.test(file)) {
            tabs.map(async ({ id }) => {
              await chrome.scripting.executeScript({ target: { tabId: id }, files: [file] });
            });
          } else {
            tabs.map(async ({ id }) => {
              await chrome.scripting.executeScript({ target: { tabId: id }, code: file });
            });
          }
        })
      );
    } catch (e) {
      console.log(e);
    }
  }

  static async setTimeout(timeout = 0) {
    return new Promise(resolve => {
      setTimeout(resolve, timeout);
    });
  }
}
