import nscCore from '@/lib/core';

export default class FragmentScrollDetected {
  static elems_scroll = [];

  static isElementHidden(elem) {
    let p = elem;

    while (p && p !== document) {
      const style = getComputedStyle(p, '');

      if (style.visibility === 'hidden' || style.display === 'none' || style.opacity === 0) return true;
      p = p.parentNode;
    }

    return false;
  }

  static searchScrollElem(parent) {
    const currents = parent.children;

    for (let i = 0, len = currents.length; i < len; i++) {
      const current = currents[i];
      const { children } = current;
      const style = getComputedStyle(current, '');

      for (let i2 = 0, len2 = children.length; i2 < len2; i2++) {
        if (!this.isElementHidden(current)
            && children[i2].clientHeight > current.clientHeight + 20
            && current.clientHeight !== 0
            && (style.overflowY === 'scroll' || style.overflowY === 'auto')) {
          this.elems_scroll.push(current);
        }
      }

      this.searchScrollElem(current);
    }
  }

  static detected(onlySearch) {
    this.elems_scroll = [];

    this.searchScrollElem(document.body);

    if (!onlySearch && this.elems_scroll.length) {
      nscCore.sendMessageSync({ operation: 'content_capture_fragment_scroll_detected' });
    }
    return this.elems_scroll;
  }
}
