import nscCore from '@/lib/core';
import LS from '@/lib/LocalStorage';
import nscExt from '@/lib/extension';

export default class ContentVideoGuide {
  static lastEvent = '';
  static lastOuterHTML = '';
  static mouseDownEvent = false;
  static dropEvent = false;

  static getInnerElem(target) {
    if (!target) {
      return null;
    } if (target.innerText === undefined || target.innerText === '') {
      return ContentVideoGuide.getInnerElem(target.parentNode);
    }
    return target;
  }

  static getInnerText(target) {
    if (!target) {
      return '';
    } if (target.innerText === undefined || target.innerText === '') {
      return /class=.+icon/.test(target.outerHTML) ? 'icon' : ContentVideoGuide.getInnerText(target.parentNode);
    }
    // eslint-disable-next-line no-restricted-syntax
    for (const child of target.children) {
      if (child.innerText) {
        return target.innerText;
      }
    }

    return target.innerText || '';
  }

  static stopEventGuide(e) {
    if (e.isTrusted) {
      e.stopPropagation();
      e.preventDefault();
    }
  }

  static handleMouseDown(e) {
    this.dropEvent = false;
    this.mouseDownEvent = e;
  }

  static handleMouseUp(e) {
    this.mouseDownEvent = false;
    if (this.dropEvent) {
      this.checkerVideoGuide(e.originalEvent);
    }
  }

  static handleMouseOver(e) {
    if (this.mouseDownEvent
      && (this.mouseDownEvent.clientY > e.clientY + 20
        || this.mouseDownEvent.clientY < e.clientY - 20
      )) {
      this.dropEvent = e;
    }
  }

  static checkerVideoGuide(e) {
    if (e.target.closest('.nsc-panel-compact') || e.target.tagName === 'BODY') {
      return;
    }

    if (this.lastEvent === 'mouseup' && e.type === 'click') {
      this.lastEvent = e.type;
      return;
    }

    let label = '';
    let element = '';
    let value = '';
    const currentTarget = e.target;

    // console.log(e, currentTarget.type, currentTarget.tagName);

    if (e.isTrusted) {
      const type = currentTarget.type && currentTarget.type.toLocaleLowerCase().replace(/-.+/, '');
      const { timeStamp } = e;
      const forId = currentTarget.id;
      const $label = document.querySelector(`*[for="${forId}"]`);
      const event = e.type;
      const { width, height, left, top } = currentTarget.getBoundingClientRect();

      this.lastEvent = e.type;
      this.lastOuterHTML = e.target.outerHTML;

      setTimeout(() => {
        const saveEvent = new e.constructor(e.type, e);
        currentTarget.dispatchEvent(saveEvent);
      }, 500);

      e.stopPropagation();
      e.preventDefault();

      if ($label) {
        label = $label.innerText;
      }

      if (event === 'input') {
        switch (type) {
          case 'password':
            return;
          case 'textarea':
          case 'text':
          case 'tel':
          case 'input':
          case 'email':
            element = 'input';
            value = currentTarget.value;
            break;
          case 'select': {
            element = 'select';
            const [option] = currentTarget.querySelectorAll('option:checked');
            value = option.innerText;
            break;
          }
          case 'radio':
            element = 'radio';
            value = `${currentTarget.checked ? 'checked' : 'unchecked'}`;
            break;
          case 'checkbox':
            element = 'checkbox';
            value = `${currentTarget.checked ? 'checked' : 'unchecked'}`;
            break;
          default:
            element = currentTarget.tagName.toLocaleLowerCase();
            value = ContentVideoGuide.getInnerText(currentTarget);
            break;
        }
      } else if (currentTarget.closest('button')) {
        element = 'button';
        value = currentTarget.closest('button').innerText.replace(/\n.+/g, '')
            || (/class.+icon/.test(currentTarget.closest('button').outerHTML) ? 'icon' : 'button');
      } else if (currentTarget.tagName === 'IMG' && !currentTarget.closest('a')) {
        element = 'img';
        value = 'Image';
      } else {
        switch (type) {
          case 'checkbox':
          case 'radio':
          case 'select':
            return;
          case 'submit':
            element = 'button';
            value = currentTarget.value;
            break;
          case 'button':
            element = 'button';
            value = currentTarget.innerText || 'button';
            break;
          case 'textarea':
          case 'text':
          case 'input':
          case 'tel':
            element = 'input';
            value = currentTarget.value;
            break;
          default:
            element = currentTarget.tagName.toLocaleLowerCase();

            if (!currentTarget.innerHTML
                && width >= window.innerWidth - 100
                && height >= window.innerHeight - 100) {
              return;
            }

            if (currentTarget.closest('label') || element === 'label') {
              const labelFor = (currentTarget.closest('label') || currentTarget).getAttribute('for');
              if (document.getElementById(labelFor)) {
                return;
              }
            }

            if (currentTarget.closest('a') && !currentTarget.innerText) {
              value = currentTarget.closest('a').innerText || 'Link';
            }

            value = ContentVideoGuide.getInnerText(currentTarget);
            break;
        }
      }

      let { x } = e;
      let { y } = e;

      if (x === undefined) {
        x = (left + width) / 2;
      }

      if (y === undefined) {
        y = (top + height) / 2;
      }

      const stepEvent = {
        element,
        value,
        event,
        label,
        width,
        height,
        left,
        top,
        x,
        y,
        z: window.devicePixelRatio,
        timeStamp
      };

      // console.log(stepEvent);
      nscCore.sendMessage({ operation: 'saveVideoGuideEvent', value: stepEvent });
    }
  }

  static eventOn() {
    /*
*  добавить зачистку в
* var iframe = document.getElementById('myIFrame');
iframe.contentDocument.body.addEventListener('mouseup', Handler);

function Handler() {
    alert('works');
}
* https://demoportal.nimbusweb.me/space/settings/org/u1x3ef проверить
*
* */

    document.addEventListener('click', this.checkerVideoGuide, true);
    document.addEventListener('input', this.checkerVideoGuide, true);
    document.addEventListener('mousedown', this.handleMouseDown, true);
    document.addEventListener('mouseup', this.handleMouseUp, true);
    document.addEventListener('mousemove', this.handleMouseOver, true);

    document.querySelectorAll('iframe').forEach(e => {
      if (e.contentDocument) {
        e.contentDocument.addEventListener('click', this.checkerVideoGuide, true);
        e.contentDocument.addEventListener('input', this.checkerVideoGuide, true);
        e.contentDocument.addEventListener('mousedown', this.handleMouseDown, true);
        e.contentDocument.addEventListener('mouseup', this.handleMouseUp, true);
        e.contentDocument.addEventListener('mousemove', this.handleMouseOver, true);
      }
    });
  }

  static eventOff() {
    document.removeEventListener('click', this.checkerVideoGuide, true);
    document.removeEventListener('input', this.checkerVideoGuide, true);
    document.removeEventListener('mousedown', this.handleMouseDown, true);
    document.removeEventListener('mouseup', this.handleMouseUp, true);
    document.removeEventListener('mousemove', this.handleMouseOver, true);

    document.querySelectorAll('iframe').forEach(e => {
      if (e.contentDocument) {
        e.contentDocument.removeEventListener('click', this.checkerVideoGuide, true);
        e.contentDocument.removeEventListener('input', this.checkerVideoGuide, true);
        e.contentDocument.removeEventListener('mousedown', this.handleMouseDown, true);
        e.contentDocument.removeEventListener('mouseup', this.handleMouseUp, true);
        e.contentDocument.removeEventListener('mousemove', this.handleMouseOver, true);
      }
    });
  }

  static async init({ status, state }) {
    const videoRecordIsGuide = await LS.getItem('videoRecordIsGuide');
    // const { status, state } = await nscExt.getScreencastStatus();

    if (status && state === 'recording' && videoRecordIsGuide) {
      // console.log('on Guide');
      this.eventOn();
    } else {
      // console.log('off Guide');
      this.eventOff();
    }
  }
}
