import nscCore from '@/lib/core';
import LS from '@/lib/LocalStorage';
import ContentHelper from '@/content/ContentHelper';
import nscExt from '@/lib/extension';

export default class ContentVideoCamera {
  static videoCameraX = 0;
  static videoCameraY = 0;
  static videoCameraXFull = 0;
  static videoCameraYFull = 0;
  static videoCameraMove = false;
  static videoCameraShiftX = 0;
  static videoCameraShiftY = 0;
  static videoCameraW = 0;
  static videoCameraH = 0;
  static videoTypeCapture = null;
  static constraints = false;
  static videoCameraEnable = false;

  static option = {};
  static eventList = [];
  static iframe = null;
  static isInit = false;

  static $content_camera = false;
  static $content_camera_container = false;
  static $content_camera_buttons = false;
  static $camera_collapse = false;
  static $camera_expand = false;
  static $camera_close = false;

  static async showCamera() {
    console.log('showCamera');
    if (this.iframe !== null) {
      return;
    }
    this.iframe = true;

    if (!document.getElementById('nsc_content_camera')) {
      const nscCamera = document.createElement('div');
      nscCamera.className = 'nsc-content-camera';
      nscCamera.id = 'nsc_content_camera';
      nscCamera.innerHTML = '<div class="nsc-content-camera-buttons" id="nsc_content_camera_buttons" flex="none" layout="row" layout-align="start'
        + ' start">\n'
        + '        <button class="nsc-content-camera-button nsc-hide" type="button" id="nsc_video_camera_collapse">\n'
        + '            <span class="nsc-icon nsc-icon-panel-collapse"></span>\n'
        + '        </button>\n'
        + '        <button class="nsc-content-camera-button" type="button" id="nsc_video_camera_expand">\n'
        + '            <span class="nsc-icon nsc-icon-panel-expand"></span>\n'
        + '        </button>\n'
        + '        <button class="nsc-content-camera-button" type="button" id="nsc_video_camera_close">\n'
        + '            <span class="nsc-icon nsc-icon-panel-close"></span>\n'
        + '        </button>\n'
        + '    </div>\n'
        + '    <div class="nsc-content-camera-container" id="nsc_content_camera_container">\n'
        + '        <div class="nsc-content-camera-shadow"></div>\n'
        + '    </div>';

      this.getElemAppend().appendChild(nscCamera);

      this.$content_camera = document.getElementById('nsc_content_camera');
      this.$content_camera_container = document.getElementById('nsc_content_camera_container');
      this.$content_camera_buttons = document.getElementById('nsc_content_camera_buttons');
      this.$camera_collapse = document.getElementById('nsc_video_camera_collapse');
      this.$camera_expand = document.getElementById('nsc_video_camera_expand');
      this.$camera_close = document.getElementById('nsc_video_camera_close');

      this.$camera_collapse.addEventListener('click', this.miniCamera);
      this.$camera_expand.addEventListener('click', this.fullCamera);
      this.$camera_close.addEventListener('click', this.hideCamera);

      document.addEventListener('fullscreenchange', async () => {
        this.getElemAppend().appendChild(this.$content_camera);
      });
    }
    // console.log('videoCameraEnable', true);
    // LS.setItem('videoCameraEnable', true);

    this.positionCamera(this.videoCameraX, this.videoCameraY);
    this.$content_camera.classList.remove('nsc-hide');

    const mediaCamera = await ContentHelper.createFrameDisplayMedia('camera', this.$content_camera_container, true);
    this.iframe = mediaCamera.iframe;

    if (!mediaCamera.value) {
      this.hideCamera();
    } else {
      this.videoCameraW = mediaCamera.value.w;
      this.videoCameraH = mediaCamera.value.h;
      this.miniCamera();

      this.$content_camera_buttons.classList.remove('nsc-hide');
      this.$content_camera.addEventListener('mousedown', this.startMove);
      this.$content_camera.addEventListener('mouseup', this.endMove);

      window.addEventListener('mousemove', this.move);
      window.addEventListener('mouseup', this.endMove);
    }
  }

  static hideCamera = () => {
    console.log('hideCamera');
    if (this.iframe === null) {
      return;
    }

    this.iframe.remove();
    this.iframe = null;
    this.$content_camera.classList.add('nsc-hide');
    this.$content_camera.removeEventListener('mousedown', this.startMove);
    this.$content_camera.removeEventListener('mouseup', this.endMove);

    window.removeEventListener('mousemove', this.move);
    window.removeEventListener('mouseup', this.endMove);
  };

  static async toggleCamera() {
    if (this.$content_camera) {
      if (this.$content_camera.classList.contains('nsc-hide')) {
        LS.setItem('videoCameraEnable', true);
        await this.showCamera();
      } else {
        LS.setItem('videoCameraEnable', false);
        this.hideCamera();
      }
    } else {
      LS.setItem('videoCameraEnable', true);
      await this.showCamera();
    }
  }

  static miniCamera = () => {
    this.$content_camera.classList.remove('full');
    this.$content_camera_container.style.width = `${320}px`;
    this.$content_camera_container.style.height = `${320 / (this.videoCameraW / this.videoCameraH)}px`;
    this.positionCamera(this.videoCameraX, this.videoCameraY);
    this.$camera_collapse.classList.add('nsc-hide');
    this.$camera_expand.classList.remove('nsc-hide');
  };

  static fullCamera = () => {
    this.$content_camera.classList.add('full');

    const width = window.innerWidth - 20;
    const height = window.innerHeight - 20;

    if (width / (this.videoCameraW / this.videoCameraH) > height) {
      this.$content_camera_container.style.width = `${height * (this.videoCameraW / this.videoCameraH)}px`;
      this.$content_camera_container.style.height = `${height}px`;
    } else {
      this.$content_camera_container.style.width = `${width}px`;
      this.$content_camera_container.style.height = `${height}px`;
    }

    this.positionCamera(10, 10);

    this.$camera_collapse.classList.remove('nsc-hide');
    this.$camera_expand.classList.add('nsc-hide');
  };

  static positionCamera(pageX, pageY) {
    const pageLeft = 10;
    const pageTop = 10;
    const pageRight = window.innerWidth - this.$content_camera.width - 10;
    const pageBottom = window.innerHeight - this.$content_camera.height - 10;

    if (pageX < pageLeft) pageX = 10;
    if (pageY < pageTop) pageY = 10;
    if (pageX > pageRight) pageX = pageRight;
    if (pageY > pageBottom) pageY = pageBottom;
    console.log('positionCamera', pageY, pageX);
    this.$content_camera.style.top = `${pageY || 10}px`;
    this.$content_camera.style.left = `${pageX || 10}px`;
  }

  static move = e => {
    if (this.videoCameraMove) {
      let pageX = e.pageX - this.videoCameraShiftX;
      let pageY = e.pageY - this.videoCameraShiftY;

      this.videoCameraShiftX = e.pageX;
      this.videoCameraShiftY = e.pageY;

      if (this.$content_camera.classList.contains('full')) {
        // eslint-disable-next-line no-multi-assign,vue/max-len
        pageX = this.videoCameraXFull = pageX + this.videoCameraXFull > 0 ? pageX + this.videoCameraXFull : 0;
        // eslint-disable-next-line no-multi-assign,vue/max-len
        pageY = this.videoCameraYFull = pageY + this.videoCameraYFull > 0 ? pageY + this.videoCameraYFull : 0;
      } else {
        // eslint-disable-next-line no-multi-assign
        pageX = this.videoCameraX = pageX + this.videoCameraX > 0 ? pageX + this.videoCameraX : 0;
        // eslint-disable-next-line no-multi-assign
        pageY = this.videoCameraY = pageY + this.videoCameraY > 0 ? pageY + this.videoCameraY : 0;
      }
      this.positionCamera(pageX, pageY);
    }
  };

  static startMove = e => {
    e.preventDefault();

    this.videoCameraMove = true;
    this.videoCameraShiftX = e.pageX;
    this.videoCameraShiftY = e.pageY;
    this.$content_camera.classList.add('nsc-move');
  };

  static endMove = async () => {
    this.videoCameraMove = false;
    if (this.$content_camera.classList.contains('full')) return;

    await nscCore.sendMessage({
      operation: 'save_position_video_camera',
      position: { x: this.videoCameraX, y: this.videoCameraY }
    });
    this.$content_camera.classList.remove('nsc-move');
  };

  static getElemAppend() {
    return document.fullscreenElement ? document.fullscreenElement : document.body;
  }

  static async init({ status }) {
    const isMediaCamera = await LS.getItem('videoCameraEnable');

    const { x, y } = await LS.getItem('videoCameraPosition');
    this.videoCameraX = x;
    this.videoCameraY = y;
    if (isMediaCamera && status) {
      await this.showCamera();
    } else {
      await this.hideCamera();
    }
  }
}
