/* eslint-disable */
import ClearCapture from "@/content/ClearCapture";
import ContentVideoGuide from "@/content/ContentVideoGuide";
import CapturePage from "@/content/CapturePage";
import ContentVideoCamera from "@/content/ContentVideoCamera";
import ContentVideoPanel from "@/content/ContentVideoPanel";
import ContentHelper from "@/content/ContentHelper";
import nscCore from "@/lib/core";
import nscExt from "@/lib/extension";
import LS from "@/lib/LocalStorage";
import ActionFragment from "@/content/ActionFragment";
import ActionScroll from "@/content/ActionScroll";
import FragmentScrollDetected from "@/content/FragmentScrollDetected";
import ActionFragmentScroll from "@/content/ActionFragmentScroll";
import ActionSelected from "@/content/ActionSelected";
import ContentWatermark from "@/content/ContentWatermark";
import ScreencastTimer from "@/content/ScreencastTimer";
import ContentPopup from '@/content/ContentPopup';
import ScreencastTabSelect from '@/content/ScreencastTabSelect';

if (!window.NSC_EXT_CONTENT_JS_INSERTED) {
  window.NSC_EXT_CONTENT_JS_INSERTED = true;

  window.addEventListener(
    "keydown",
    (e) => {
      if (e.key === "Escape") {
        ActionFragment.viewClear();
        ActionFragment.eventClear();
        ActionFragment.eventStop(e);

        ActionFragmentScroll.viewClear();
        ActionFragmentScroll.eventClear();
      }
    },
    false
  );

  let match = document.cookie.match("(?:^|;)\\s*_ga=([^;]*)");
  const raw = match ? decodeURIComponent(match[1]) : null;

  if (raw) {
    match = raw.match(/(\d+\.\d+)$/);
  }
  const gacid = match ? match[1] : null;
  LS.setItem("gaUserId", gacid);

  const quickVideoCaptureGithub = LS.getItem("quickVideoCaptureGithub");
  const quickVideoCaptureGithubWelcome = LS.getItem(
    "quickVideoCaptureGithubWelcome"
  );

  if (location.origin === "https://github.com") {
    if (quickVideoCaptureGithub) {
      ContentHelper.showVideoStartGithub();
    }
    if (!quickVideoCaptureGithub && quickVideoCaptureGithubWelcome) {
      ContentHelper.showWelcomeGithub();
    }
  }

  window.addEventListener(
    "keydown",
    async (e) => {
      const hotkeys = await LS.getItem("appHotkeys");
      const k = e.keyCode;

      if (e.shiftKey && (e.ctrlKey || e.metaKey)) {
        if (k === +hotkeys.entire) {
          await nscCore.sendMessage({operation: 'startCreateScreenshot', action: 'entire'});
        }
        if (k === +hotkeys.fragment) {
          await nscCore.sendMessage({operation: 'startCreateScreenshot', action: 'fragment'});
        }
        if (k === +hotkeys.selected) {
          await nscCore.sendMessage({operation: 'startCreateScreenshot', action: 'selected'});
        }
        if (k === +hotkeys.scroll) {
          await nscCore.sendMessage({operation: 'startCreateScreenshot', action: 'scroll'});
        }
        if (k === +hotkeys.visible) {
          await nscCore.sendMessage({ operation: 'startCreateScreenshot', action: 'visible'});
        }
        if (k === +hotkeys.window) {
          await nscCore.sendMessage({ operation: 'startCreateScreenshot', action: 'window'});
        }
      }
    },
    false
  );

  chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    console.log("runtime onMessage content", JSON.stringify(request), sender);

    switch (request.operation) {
      case 'contentPopupPremiumOpen':
        (async function getScreenshotUrl() {
          await ContentPopup.premiumOpen(request);
        }());
        break;
      case 'contentPopupLoginOpen':
        (async function getScreenshotUrl() {
          await ContentPopup.loginOpen(request);
        }());
        break;
      case 'getScreenshotDetails':
        (async function getScreenshotUrl() {
          const { info, format, parts } = request;
          const { blob } = await nscExt.createCanvasParts(info, format, parts);
          const url = URL.createObjectURL(blob);
          const fileName = await nscExt.getFileNameImage(format);
          sendResponse({
            url,
            fileName,
            format,
          });
        }());
        break;
      case 'getUserMediaCamera':
      case 'getUserMediaMicrophone':
        (async function getUserMediaCamera() {
          await ContentHelper.createFrameDisplayMedia(request.operation);
          sendResponse(true);
        })();
        break;
      case "contentFragmentInit":
        ActionFragment.init();
        break;
      case "contentFragmentScrollInit":
        (async function contentFragmentScrollInit() {
          await ActionFragmentScroll.init();
        })();
        break;
      case "startTabSelectScreencast":
        (async function startTabSelectScreencast() {
          await ScreencastTabSelect.init();
          sendResponse(true);
        })();
        break;
      case "content_capture_fragment_scroll_search":
        FragmentScrollDetected.detected();
        break;
      case "nsc_content_check_ready":
        sendResponse(true);
        break;
      case "capture_entire_page":
        (async function CapturePageGetPositions() {
          await CapturePage.getPositions();
        })();
        break;
      case "capture_fragment_entire_page":
        (async function getPositions() {
          const { afterAction } = request;
          await CapturePage.getPositions(true, false, false, afterAction);
        })();
        break;
      case "capture_fragment_scroll_entire_page":
        (async function getPositions() {
          const { afterAction } = request;
          await CapturePage.getPositions(false, true, false, afterAction);
        })();
        break;
      case "capture_scroll_entire_page":
        (async function getPositions() {
          const { afterAction } = request;
          await CapturePage.getPositions(false, false, true, afterAction);
        })();
        break;
      case "capture_full_page_stop":
        CapturePage.stop = true;
        sendResponse(true);
        break;
      case "crop_data_screen":
        (async function () {
          await ActionSelected.init(request.dataUrl);
        })();
        break;
      case "scroll_crop_data_screen":
        (async function () {
          await ActionScroll.init();
        })();
        break;
      case "contentScrollbarInvisible":
        ClearCapture.removeStyleSheet("nsc-disable-scroll-capture-body");
        ClearCapture.removeStyleSheet("nsc-disable-transition-capture");
        sendResponse(true);
        break;
      case "contentScrollbarVisible":
        ClearCapture.addStyleSheet(
          "nsc-disable-scroll-capture-body",
          "html::-webkit-scrollbar,body::-webkit-scrollbar{width: 0 !important; height: 0 !important;} html,body{scrollbar-width: none !important;}"
        );
        ClearCapture.addStyleSheet(
          "nsc-disable-transition-capture",
          "*{transition: none !important; transition-delay: 0s !important; animation-duration: 0s !important; animation-delay: 0s !important;}"
        );
        sendResponse({
          w: window.innerWidth,
          h: window.innerHeight,
          z: window.devicePixelRatio,
          isMobile: ContentHelper.is_mobile
        });
        break;
      case "content_automation_status_upload":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("popupAutomationUpload"),
          true
        );
        sendResponse(true);
        break;
      case "content_automation_status_upload_stream":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("popupAutomationUploadStream")
        );
        break;
      case "content_automation_status_storage_limit":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("popupAutomationStorageLimit")
        );
        ContentHelper.hideUploadBox();
        break;
      case "content_automation_stream_error":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("popupAutomationStreamError")
        );
        ContentHelper.hideUploadBox();
        break;
      case "content_automation_status_copy":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("notificationImageCopied")
        );
        ContentHelper.hideUploadBox(5000);
        break;
      case "content_automation_status_not_copy":
        ContentHelper.showUploadBox(
          chrome.i18n.getMessage("notificationImageNotCopied")
        );
        ContentHelper.hideUploadBox(5000);
        sendResponse(true);
        break;
      case "content_automation_status_upload_end":
        ContentHelper.hideUploadBox(0);
        sendResponse(true);
        break;
      case "content_automation_send_url":
        (async function () {
          const {
            site,
            url
          } = request;

          if (site && site === "github") {
            const textarea = document.getElementById("new_comment_field")
              ? document.getElementById("new_comment_field")
              : document.getElementById("issue_body");

            textarea.value = `${textarea.value}\n${url}`;
          } else if (site && site === "gmail") {

          } else {
            const copy = await ContentHelper.copyUrl(url);

            if (copy) {
              ContentHelper.showUploadBox(copy);
            } else {
              nscCore.sendMessageSync({operation: "open_page",url });
            }

            ContentHelper.hideUploadBox();
          }
        })()
        break;
      case "copy_to_clipboard":
        (async function () {
          const { info, parts } = request;
          const { blob } = await nscExt.createCanvasParts(info, parts);

          if (navigator.clipboard && navigator.clipboard.write) {
            const item = new ClipboardItem({ "image/png": blob });

            navigator.clipboard.write([item]).then(
              () => {
                nscCore.sendMessageSync({
                  operation: "content_message",
                  message: { operation: "content_automation_status_copy" },
                });
              },
              () => {
                nscCore.sendMessageSync({
                  operation: "content_message",
                  message: { operation: "content_automation_status_not_copy" },
                });
              }
            );
          } else {
            console.error("error copy, navigator.clipboard.write undefined");
          }
        })();
        break;
      case "screencastUpdateStatus":
          (async function screencastUpdateStatus() {
              await ContentWatermark.init(request);
              await ContentVideoCamera.init(request);
              await ContentVideoPanel.init(request);
              await ContentVideoGuide.init(request);

              sendResponse(true);
          })();
        return true
      case "showContentVideoPanel":
        (async function showContentVideoPanel() {
          await ContentVideoPanel.init({isShowContentVideoPanel: true});
          sendResponse(true);
        })();
        return true
      case "startScreencastTimer":
          (async function StartScreencastTimer() {
            await ScreencastTimer.init();
            sendResponse(true);
          })();
          return true
      default:
        break;
    }

    return true;
  });

  window.addEventListener('message', async function (e) {
    if(e.data.type && e.data.type === "EverHelperExtMessage" && e.data.data.sessionId) {
      LS.setItem('nimbusSessionId', e.data.data.sessionId)

     await nscCore.sendMessage({operation: 'accessTokenNimbus', data: e.data});
    }
  }, false);
}
