import nscCore from '@/lib/core';
// import ClearCapture from '@/content/ClearCapture';
import ContentCropper from '@/content/ContentCropper';
import LS from '@/lib/LocalStorage';
import ContentHelper from '@/content/ContentHelper';

class ActionSelected {
  static active = false;

  static async init(image) {
    this.active = true;
    // ClearCapture.afterClearCapture();

    await ContentCropper.setImage(image);
    ContentCropper.setOptions({ wrapperFull: false, scrollEnable: false });
    ContentCropper.mounted();
    await ContentCropper.openPopup();

    const saveCropPosition = await LS.getItem('saveCropPosition');
    if (saveCropPosition) {
      const { x, y, w, h } = await LS.getItem('selectedPosition');

      if (x && y && w && h) {
        const { pageYOffset, pageXOffset } = window;

        ContentCropper.setPoints({ x: x + pageXOffset, y: y + pageYOffset, w, h });
        ContentCropper.render();
      }
    }
  }
}

ContentCropper.on('removePopup', async () => {
  if (!ActionSelected.active) return;

  await LS.setItem('popupActionMessageCrop', true);
});

ContentCropper.on('destroy', () => {
  if (!ActionSelected.active) return;
  ActionSelected.active = false;
  // ClearCapture.beforeClearCapture();
});

ContentCropper.on('end', async () => {
  if (!ActionSelected.active) return;
  const z = window.devicePixelRatio;
  const isMobile = ContentHelper.is_mobile;
  await LS.setItem('selectedPosition', { z, isMobile, ...ContentCropper.getFinishPoints() });
});

ContentCropper.on('action', async afterAction => {
  if (!ActionSelected.active) return;

  switch (afterAction) {
    case 'edit':
    case 'download':
    case 'nimbus':
    case 'slack':
    case 'google':
    case 'quick':
    case 'print':
    case 'pdf':
    case 'copy_to_clipboard':
      await nscCore.sendMessage({ operation: 'selectedCapture', afterAction });
      break;
    default:
      break;
  }
});

export default ActionSelected;
