import nscCore from '@/lib/core';
import ContentHelper from '@/content/ContentHelper';
import FragmentScrollDetected from '@/content/FragmentScrollDetected';
import LS from '@/lib/LocalStorage';

export default class ActionFragmentScroll {
  static position = {};
  static elem = null;
  static appType = null;

  static savePosition() {
    LS.setItem('fragmentScrollPosition', this.position);
  }

  static eventStop = e => {
    e.preventDefault();
    e.stopPropagation();
    return false;
  };

  static eventClear() {
    this.eventElements(document.body, true);

    'mouseenter mouseleave mousemove touchmove mouseup touchend'.split(' ').forEach(e => {
      document.body.removeEventListener(e, this.viewBorder, false);
    });

    document.removeEventListener('contextmenu', this.eventContextMenu, false);
  }

  static eventElements(elem, remove) {
    if (remove) {
      elem.removeEventListener('click', this.eventStop);
    } else {
      elem.addEventListener('click', this.eventStop);
    }

    for (const child of elem.children) {
      this.eventElements(child);
    }
  }

  static eventContextMenu = e => {
    this.eventClear();
    this.eventStop(e);
  };

  static viewClear() {
    $('#nsc_capture_fragment_box').remove();
    $('.nsc-capture-fragment-scroll-box').remove();
    $('.nsc-popup-action-message').remove();
    ContentHelper.deleteElementBuId('nsc_capture_fragment_scroll_box');
    ContentHelper.deleteElementBuId('nsc_popup_action_message');
    ContentHelper.deleteElementBuId('nsc_capture_fragment_box');
  }

  static viewBorder = e => {
    this.elem = e.target;

    // if (this.elem.classList.contains("nsc-capture-fragment-box")) {
    //   return;
    // }

    this.position = {
      x: Math.round(this.elem.offsetLeft),
      y: Math.round(this.elem.offsetTop),
      w: Math.round(this.elem.outerWidth),
      h: Math.round(this.elem.outerHeight)
    };

    if (!document.getElementsByClassName('nsc-capture-fragment-box').length) {
      const div = document.createElement('div');

      div.className = 'nsc-capture-fragment-box';
      div.id = 'nsc_capture_fragment_box';

      document.body.appendChild(div);
    }

    const nscCaptureFragmentBox = document.getElementById('nsc_capture_fragment_box');

    nscCaptureFragmentBox.style.top = this.position.y - 3;
    nscCaptureFragmentBox.style.left = this.position.x - 3;
    nscCaptureFragmentBox.style.width = this.position.w - 9;
    nscCaptureFragmentBox.style.height = this.position.h - 9;
  };

  static viewButton() {
    'mouseenter mousemove touchmove'.split(' ').forEach(e => {
      document.body.removeEventListener(e, this.viewBorder, false);
    });

    // this.eventClear();

    const ns_crop_buttons = document.createElement('div');
    const ns_crop_button_edit = document.createElement('button');
    const ns_crop_button_save = document.createElement('button');
    const ns_crop_button_cancel = document.createElement('button');

    ns_crop_buttons.className = 'ns-crop-buttons bottom';
    ns_crop_buttons.id = 'screenshotbutton';

    ns_crop_button_edit.className = 'ns-btn edit';
    ns_crop_button_edit.innerHTML(`<i></i><span>${chrome.i18n.getMessage('cropBtnEdit')}</span>`);
    ns_crop_button_edit.addEventListener('click', () => {
      nscCore.sendMessageSync({ operation: 'screenshotFragmentScrollFinish', path: 'redactor_fragment' });
      this.viewClear();
    });

    ns_crop_button_save.className = 'ns-btn save';
    ns_crop_button_save.innerHTML(`<i></i><span>${chrome.i18n.getMessage('cropBtnSave')}</span>`);
    ns_crop_button_save.addEventListener('click', () => {
      nscCore.sendMessageSync({ operation: 'send_to', path: 'download_fragment' });
      this.viewClear();
    });

    ns_crop_button_cancel.className = 'ns-btn cancel';
    ns_crop_button_cancel.innerHTML(`<i></i><span>${chrome.i18n.getMessage('cropBtnCancel')}</span>`);
    ns_crop_button_cancel.addEventListener('click', () => {
      this.eventClear();
    });

    ns_crop_buttons.appendChild(ns_crop_button_edit);
    ns_crop_buttons.appendChild(ns_crop_button_save);
    ns_crop_buttons.appendChild(ns_crop_button_cancel);

    const ns_crop_more = document.createElement('div');

    ns_crop_buttons.className = 'ns-crop-more';
    ns_crop_buttons.id = 'ns_crop_more';
    ns_crop_buttons.innerHTML('<button></button>');

    const ns_more_container = document.createElement('div');

    ns_more_container.className = 'ns-crop-more-container';
    ns_more_container.id = 'ns_more_container';

    if (this.appType === 'nimbus') {
      const ns_crop_button_nimbus = document.createElement('button');

      ns_crop_button_nimbus.className = 'ns-btn nimbus';
      ns_crop_button_nimbus.innerHTML('<span>Nimbus</span>');
      ns_crop_button_cancel.addEventListener('click', () => {
        nscCore.sendMessageSync({ operation: 'send_to', path: 'nimbus_fragment' });
        this.eventClear();
        this.viewClear();
      });

      ns_more_container.appendChild(ns_crop_button_cancel);

      const ns_crop_button_slack = document.createElement('button');

      ns_crop_button_slack.className = 'ns-btn slack';
      ns_crop_button_slack.innerHTML('<span>Slack</span>');
      ns_crop_button_slack.addEventListener('click', () => {
        nscCore.sendMessageSync({ operation: 'send_to', path: 'slack_fragment' });
        this.eventClear();
      });

      ns_more_container.appendChild(ns_crop_button_slack);
    }

    const ns_crop_button_google = document.createElement('button');

    ns_crop_button_google.className = 'ns-btn google';
    ns_crop_button_google.innerHTML('<span>Google Drive</span>');
    ns_crop_button_google.addEventListener('click', () => {
      nscCore.sendMessageSync({ operation: 'send_to', path: 'google_fragment' });
      this.eventClear();
    });

    ns_more_container.appendChild(ns_crop_button_google);

    // if (this.appType === 'nimbus') {
    //   const ns_crop_button_quick = document.createElement('button');
    //
    //   ns_crop_button_quick.className = 'ns-btn quick';
    //   ns_crop_button_quick.innerHTML(`<span>${chrome.i18n.getMessage('editBtnQuickUpload')}</span>`);
    //   ns_crop_button_quick.addEventListener('click', () => {
    //     nscCore.sendMessageSync({ operation: 'send_to', path: 'quick_fragment' });
    //     this.eventClear();
    //   });
    //
    //   ns_more_container.appendChild(ns_crop_button_quick);
    // }

    const ns_crop_button_print = document.createElement('button');

    ns_crop_button_print.className = 'ns-btn print';
    ns_crop_button_print.innerHTML('<span>Print</span>');
    ns_crop_button_print.addEventListener('click', () => {
      nscCore.sendMessageSync({ operation: 'send_to', path: 'print_fragment' });
      this.eventClear();
    });

    ns_more_container.appendChild(ns_crop_button_print);

    const ns_crop_button_pdf = document.createElement('button');

    ns_crop_button_pdf.className = 'ns-btn pdf';
    ns_crop_button_pdf.innerHTML(`<span>${chrome.i18n.getMessage('cropBtnSavePdf')}</span>`);
    ns_crop_button_pdf.addEventListener('click', () => {
      nscCore.sendMessageSync({ operation: 'send_to', path: 'pdf_fragment' });
      this.eventClear();
    });

    ns_more_container.appendChild(ns_crop_button_pdf);

    if (ContentHelper.is_firefox) {
      const ns_crop_button_copy = document.createElement('button');

      ns_crop_button_copy.className = 'ns-btn copy';
      ns_crop_button_copy.innerHTML(`<span>${chrome.i18n.getMessage('cropBtnCopy')}</span>`);
      ns_crop_button_copy.addEventListener('click', () => {
        nscCore.sendMessageSync({ operation: 'send_to', path: 'copy_to_clipboard_fragment' });
        this.eventClear();
      });

      ns_more_container.appendChild(ns_crop_button_copy);
    }

    const { w, h, y } = this.position;

    if (h + y + 100 > window.innerHeight) {
      ns_crop_buttons.style.top = 'auto';
      ns_crop_buttons.style.bottom = '0';
      ns_crop_more.style.top = 'auto';
      ns_crop_more.style.bottom = '0';
    } else {
      ns_crop_buttons.style.top = '100%';
      ns_crop_buttons.style.bottom = 'auto';
      ns_crop_more.style.top = '100%';
      ns_crop_more.style.bottom = 'auto';
    }

    if (w < 500) {
      ns_crop_more.style.top = 'auto';
      ns_crop_more.style.bottom = '0';
    }

    if (y + h < 400) {
      ns_more_container.style.marginTop = '5px';
      ns_more_container.style.bottom = 'auto';
    } else {
      ns_more_container.style.marginTop = '0';
      ns_more_container.style.bottom = '100%';
    }

    ns_crop_more.appendChild(ns_more_container);
    const nsc_capture_fragment_box = document.getElementById('nsc_capture_fragment_box');

    nsc_capture_fragment_box.appendChild(ns_crop_buttons);
    nsc_capture_fragment_box.appendChild(ns_crop_more);
    nsc_capture_fragment_box.classList.add('nsc-select');

    this.savePosition();

    ContentHelper.pageScroll(0, y + h - window.innerHeight + 100).then(r => {});
  }

  static async init() {
    const size = ContentHelper.getSizePage();
    const elemsScroll = FragmentScrollDetected.detected(true);
    const scrollBox = document.createElement('div');

    if (!elemsScroll.length) {
      return;
    }

    scrollBox.className = 'nsc-capture-fragment-scroll-box';
    scrollBox.id = 'nsc_capture_fragment_scroll_box';
    scrollBox.style.width = size.w;
    scrollBox.style.height = size.h;

    // eslint-disable-next-line no-restricted-syntax
    for (const elem of elemsScroll) {
      const style = elem.getBoundingClientRect();
      const scrollElem = document.createElement('div');

      scrollElem.className = 'nsc-capture-fragment-scroll-elem';
      scrollElem.id = 'nsc_capture_fragment_scroll_elem';
      scrollElem.style.width = style.width - 9;
      scrollElem.style.height = style.height - 9;
      scrollElem.style.top = style.top + 3;
      scrollElem.style.left = style.left + 3;

      scrollElem.addEventListener('click', () => {
        ContentHelper.deleteElementBuId('nsc_capture_fragment_scroll_box');
        this.viewBorder(elem);
        this.viewButton(elem);
      });

      scrollBox.appendChild(scrollElem);
    }

    this.eventElements(document.body);
    document.body.appendChild(scrollBox);

    const popupActionMessageFragmentScroll = await LS.getItem('popupActionMessageFragmentScroll');
    if (popupActionMessageFragmentScroll) {
      const nscPopupActionMessage = document.createElement('div');
      nscPopupActionMessage.innerText = chrome.i18n.getMessage('popupActionMessageFragmentScroll');
      nscPopupActionMessage.className = 'nsc-popup-action-message';
      nscPopupActionMessage.id = 'nsc_popup_action_message';

      document.body.appendChild(nscPopupActionMessage);

      await nscCore.setTimeout(3000);
      ContentHelper.deleteElementBuId('nsc_popup_action_message');
      LS.setItem('popupActionMessageFragmentScroll', true);
    }

    document.addEventListener('contextmenu', this.eventContextMenu);
  }
}
