import ClearCapture from '@/content/ClearCapture';
import LS from '@/lib/LocalStorage';
import nscCore from '@/lib/core';
import ContentHelper from '@/content/ContentHelper';

const disableScroll = () => {
  window.onscroll = e => e.stopImmediatePropagation();
  window.onwheel = e => e.preventDefault();
  window.ontouchmove = e => e.preventDefault();
  document.onkeydown = e => {
    const keys = { 37: 1, 38: 1, 39: 1, 40: 1 };

    if (keys[e.code]) e.preventDefault();
  };
};

const enableScroll = () => {
  window.onscroll = null;
  window.onwheel = null;
  window.ontouchmove = null;
  document.onkeydown = null;
};

const getStyle = elem => {
  const style = document.defaultView.getComputedStyle(elem, '');

  return {
    position: style.getPropertyValue('position'),
    overflow: style.getPropertyValue('overflow'),
    overflowY: style.getPropertyValue('overflow-y'),
    overflowX: style.getPropertyValue('overflow-x'),
    display: style.getPropertyValue('display'),
    visibility: style.getPropertyValue('visibility'),
    opacity: style.getPropertyValue('opacity')
  };
};

const getNodes = () => {
  let node;
  let current;
  const nodes = [];

  for (
    node = document.createNodeIterator(
      document.body,
      NodeFilter.SHOW_ELEMENT,
      null,
      false
    );
    // eslint-disable-next-line no-cond-assign
    (current = node.nextNode()) !== null;

  ) nodes.push(current);
  return nodes;
};

const getScrollSize = scrollSize => {
  const {
    scrollHeight,
    offsetHeight,
    clientHeight,
    scrollWidth,
    offsetWidth,
    clientWidth
  } = document.documentElement;

  // if (scrollSize) {
  //   return {
  //     height: Math.max(scrollHeight, offsetHeight, clientHeight),
  //     width: scrollSize.width
  //   };
  // }

  return {
    height: Math.max(scrollHeight, offsetHeight, clientHeight),
    width: Math.max(scrollWidth, offsetWidth, clientWidth)
  };
};

const isFly = elem => {
  const style = getStyle(elem);

  return (
    (style.position === 'fixed' || style.position === 'sticky')
    && style.display !== 'none'
    && style.visibility !== 'hidden'
    && style.opacity !== '0'
  );
};

const isScrollable = elem => {
  const style = getStyle(elem);
  return (style.overflow === 'scroll' || style.overflow === 'auto'
      || style.overflowY === 'scroll' || style.overflowY === 'auto'
      || style.overflowX === 'scroll' || style.overflowX === 'auto')
    && style.display !== 'none' && style.visibility !== 'hidden' && style.opacity !== '0';
};

function getRects(elem) {
  const rects = elem.getClientRects();
  return {
    x: rects[0].left,
    y: rects[0].top,
    w: rects[0].width,
    h: rects[0].height
  };
}

const searchScrolled = () => {
  const scrollSize = getScrollSize();
  const nodes = getNodes();
  // eslint-disable-next-line no-restricted-syntax
  for (const node of nodes) {
    if (isScrollable(node)
      && node.scrollWidth > 0 && node.scrollHeight > 0
      && (node.scrollWidth > node.clientWidth || node.scrollHeight > node.clientHeight) && (
    // eslint-disable-next-line no-mixed-operators
      node.scrollWidth > scrollSize.width && node.scrollHeight > 0.5 * scrollSize.height
      // eslint-disable-next-line no-mixed-operators
        || node.scrollHeight > scrollSize.height && node.scrollWidth > 0.5 * scrollSize.width
      // eslint-disable-next-line no-mixed-operators
        || node.clientWidth > 0.7 * scrollSize.width && node.clientHeight > 0.7 * scrollSize.height
    )) {
      return { node, rects: getRects(node) };
    }
  }
  return false;
};

export default class CapturePage {
  static fixedElements = [];
  static fragment = false;
  static fragmentScroll = false;
  static scroll = false;
  static cancel = false;
  static stop = false;
  static afterAction = false;
  static scrollSize = {
    width: 0,
    height: 0
  };

  static async pageScroll(offsetX, offsetY, elem) {
    const scrollElem = elem || document.scrollingElement || document.body;

    scrollElem.scrollLeft = offsetX;
    scrollElem.scrollTop = offsetY;
    await nscCore.setTimeout(100);
  }

  static async sendScreenData(data) {
    const actionEntirePageScrollDelay = (await LS.getItem('actionEntirePageScrollDelay'));
    // console.log('Content page, send data', data);

    // eslint-disable-next-line no-undef
    return new Promise(resolve => {
      window.setTimeout(() => {
        chrome.runtime.sendMessage(data, resolve);
      }, actionEntirePageScrollDelay);
    });
  }

  // eslint-disable-next-line default-param-last
  static async getPositions(fragment = false, fragmentScroll = false, scroll = false, afterAction = '') {
    if (!fragment) {
      ClearCapture.afterClearCapture(fragment || fragmentScroll || scroll);
    }
    disableScroll();
    this.afterAction = afterAction;
    this.scrollSize = getScrollSize();

    if (fragment) {
      const { x, y, h, w } = await LS.getItem('fragmentPosition');
      this.fragment = { x, y, h, w };
      return this.stepCapture(0, y);
    }
    if (fragmentScroll) {
      const { x, y, h, w } = await LS.getItem('fragmentScrollPosition');
      this.fragmentScroll = { x, y, h, w };
      return this.stepCapture(0, y);
    }
    if (scroll) {
      const { x, y, h, w } = await LS.getItem('scrollPosition');
      this.scroll = { x, y, h, w };
      return this.stepCapture(0, y);
    }
    return this.stepCapture(0, 0);
  }

  static async endCapture() {
    await this.enableFixedPosition(false);

    ClearCapture.beforeClearCapture(this.fragment || this.fragmentScroll || this.scroll);
    enableScroll();
    this.scroll = false;
    this.fragmentScroll = false;
    this.fragment = false;
  }

  static async stepCapture(offsetX, offsetY) {
    const progressX = offsetX + window.innerWidth < this.scrollSize.width
      && offsetX + window.innerWidth < 32767
      && (offsetY + window.innerHeight) * (offsetX + window.innerWidth)
        < 268435456;
    const progressY = offsetY + window.innerHeight < this.scrollSize.height - 1
      && offsetY + window.innerHeight < 32767
      && (offsetY + window.innerHeight) * (offsetX + window.innerWidth)
        < 268435456;

    const scrolledElement = searchScrolled();

    if (scrolledElement) {
      const classId = `.${scrolledElement.node.className.split(' ').join('.')}`;
      ClearCapture.addStyleSheet(
        `nsc-disable-scroll-${classId}`,
        `${classId}{scrollbar-width: none !important} ${classId}::-webkit-scrollbar{width: 0 !important; height: 0 !important}`
      );
    }
    // console.log(scrolledElement);

    const data = {
      operation: 'entireCapture',
      status: 'progress',
      x: 0,
      y: 0,
      x2: offsetX,
      y2: offsetY,
      w: window.innerWidth,
      h: window.innerHeight,
      pageWidth: this.scrollSize.width,
      pageHeight: this.scrollSize.height,
      scrolledElement: false,
      z: window.devicePixelRatio,
      afterAction: this.afterAction,
      isMobile: ContentHelper.is_mobile
    };

    if (this.fragment) {
      const { x, y, h, w } = this.fragment;

      data.x = x;
      data.y2 -= y;
      data.w = window.innerWidth;
      data.h = window.innerHeight - (y + h < offsetY + window.innerHeight ? offsetY + window.innerHeight - (y + h) : 0);
      data.pageWidth = w;
      data.pageHeight = h;

      if (offsetX + window.innerWidth > x + w && offsetY + window.innerHeight > y + h) {
        data.status = 'finish';
      }
    }

    if (this.fragmentScroll) {
      const { x, y, h, w } = this.fragmentScroll;

      data.x = x;
      data.y2 -= y;
      data.w = window.innerWidth;
      data.h = window.innerHeight - (y + h < offsetY + window.innerHeight ? offsetY + window.innerHeight - (y + h) : 0);
      data.pageWidth = w;
      data.pageHeight = h;

      if (offsetX + window.innerWidth > x + w && offsetY + window.innerHeight > y + h) {
        data.status = 'finish';
      }
    }

    if (this.scroll) {
      const { x, y, h, w } = this.scroll;

      data.x = x;
      data.y2 -= y;
      data.w = window.innerWidth;
      data.h = window.innerHeight - (y + h < offsetY + window.innerHeight ? offsetY + window.innerHeight - (y + h) : 0);
      data.pageWidth = w;
      data.pageHeight = h;

      if (offsetX + window.innerWidth > x + w && offsetY + window.innerHeight > y + h) {
        data.status = 'finish';
      }
    }

    if (this.cancel) {
      this.cancel = false;
      await this.pageScroll(offsetX, offsetY);
      data.status = 'cancel';
    } else if (this.stop) {
      this.stop = false;
      await this.pageScroll(offsetX, offsetY);
      data.status = 'finish';
      data.pageWidth = data.w + data.x2;
      data.pageHeight = data.h + data.y2;
    } else if (progressX || progressY) {
      await this.pageScroll(offsetX, offsetY);
    } else if (scrolledElement) { // TODO: only vertical scroll
      data.scrolledElement = true;
      data.pageHeight = (scrolledElement.node.scrollHeight + scrolledElement.rects.y)/* * z */;

      if (scrolledElement.rects.y > offsetY) { // header
        data.h = scrolledElement.rects.y;
        await this.pageScroll(offsetX, offsetY);
        // eslint-disable-next-line brace-style
      }
      /* else if (scrolledElement.rects.y + scrolledElement.rects.h <= offsetY) { // footer
                        data.y = (scrolledElement.rects.y + scrolledElement.rects.h) * z;
                        data.h = (window.innerHeight - scrolledElement.rects.y + scrolledElement.rects.h) * z;
                        window.nimbus_core.pageScroll(offsetX, offsetY);
                    } */ else { // content
        data.y = scrolledElement.rects.y/* * z */;
        data.h = scrolledElement.rects.h/* * z */;

        await this.pageScroll(offsetX, offsetY - scrolledElement.rects.y, scrolledElement.node);
        if (scrolledElement.node.scrollHeight < offsetY + scrolledElement.rects.h) {
          data.status = 'finish';
        }
      }
    } else {
      await this.pageScroll(offsetX, offsetY);
      data.status = 'finish';
    }

    await nscCore.setTimeout(150);

    if (this.scroll) {
      await this.enableFixedPosition(true/* offsetY - this.scroll.y !== 0 */);
    } else if (data.scrolledElement) {
      await this.enableFixedPosition(scrolledElement.node.scrollTop > 0, true);
    } else {
      await this.enableFixedPosition(offsetY !== 0);
    }

    await nscCore.setTimeout(150);

    if (this.fragment || this.fragmentScroll || this.scroll) {
      const { pageYOffset } = window;

      data.y = offsetY - pageYOffset;
    }

    await this.sendScreenData(data);
    this.scrollSize = getScrollSize(/* this.scrollSize */);

    if (data.status !== 'finish' && data.status !== 'cancel') {
      if (data.scrolledElement) {
        if (scrolledElement.rects.y > offsetY) { // start offset
          offsetY = scrolledElement.rects.y;
        } else if (scrolledElement.node.scrollHeight >= offsetY + scrolledElement.rects.h) { // content offset
          offsetY += scrolledElement.rects.h;
        } else { // finish offset
          offsetY += scrolledElement.rects.h;
        }
      } else if (this.scrollSize.width > offsetX + window.innerWidth) {
        offsetX = Math.min(offsetX + window.innerWidth, this.scrollSize.width - window.innerWidth);
      } else {
        offsetX = 0;
        offsetY = Math.min(offsetY + window.innerHeight, this.scrollSize.height - window.innerHeight - 1);
      }
      await this.stepCapture(offsetX, offsetY);
    } else {
      await this.endCapture();
    }
  }

  // eslint-disable-next-line consistent-return
  static async enableFixedPosition(enableFlag) {
    if (!(await LS.getItem('actionHideFixedElements'))) return false;
    if (enableFlag) {
      // eslint-disable-next-line no-restricted-syntax
      for (const node of getNodes()) {
        if (isFly(node)) {
          if (!node.classList.contains('nsc_enable_fixed_position')) {
            node.classList.add('nsc_enable_fixed_position');
            this.fixedElements.push({ element: node, cssText: node.style.cssText });
          }
          node.style.cssText = `${node.style.cssText}opacity: 0!important; animation: unset!important; transition-duration: 0s!important;`;
        }
      }
    } else {
      // eslint-disable-next-line no-restricted-syntax
      for (const elem of this.fixedElements) {
        elem.element.classList.remove('nsc_enable_fixed_position');
        elem.element.style.cssText = elem.cssText;
      }
      this.fixedElements = [];
    }
  }
}
