import nscCore from '@/lib/core';
import LS from '@/lib/LocalStorage';

export default class ScreencastTimer {
  static progress = 0;
  static progressBar = null;
  static progressBarTimer = null;

  static async init() {
    const videoCountdown = await LS.getItem('videoCountdown');
    const timerEl = document.querySelector('.nsc-timer');
    if (!timerEl) {
      await this.insertHtml();
    }
    this.showTimer(videoCountdown);
  }

  static showTimer(videoCountdown) {
    const timerEl = document.querySelector('.nsc-timer');
    if (timerEl) {
      this.setProgressBar(videoCountdown);
      timerEl.classList.remove('nsc-hide');
    }
  }

  static hideTimer() {
    if (this.progressBarTimer) {
      clearInterval(this.progressBarTimer);
      this.progressBarTimer = null;
    }

    if (this.progressBar) {
      this.progressBar = null;
    }

    const timerEl = document.querySelector('.nsc-timer');
    if (timerEl) {
      timerEl.remove();
    }

    nscCore.sendMessage({ operation: 'stopScreencastTimer' });
  }

  static async insertHtml(type = '') {
    this.progress = 0;
    this.progressBar = null;
    this.progressBarTimer = null;

    const timerEl = document.createElement('div');
    timerEl.classList.add('nsc-timer', 'nsc-hide');

    const timerContentEl = document.createElement('div');
    timerContentEl.classList.add('nsc-timer-content');

    const timerProgressEl = document.createElement('div');
    timerProgressEl.classList.add('nsc-timer-progress');

    const timerSepEl = document.createElement('div');
    timerSepEl.classList.add('nsc-timer-sep');

    const timerCancelEl = document.createElement('div');
    timerCancelEl.classList.add('nsc-timer-cancel');

    const cancelButtonEl = document.createElement('button');
    cancelButtonEl.classList.add('nsc-timer-button');
    cancelButtonEl.innerText = chrome.i18n.getMessage('panelCancel');

    cancelButtonEl.addEventListener('click', () => {
      this.hideTimer();
    });

    timerContentEl.appendChild(timerProgressEl);
    timerCancelEl.appendChild(cancelButtonEl);

    timerEl.appendChild(timerContentEl);
    timerEl.appendChild(timerSepEl);
    timerEl.appendChild(timerCancelEl);

    if (type === 'capture_delayed') {
      timerEl.classList.add('capture-delayed');
    } else {
      timerEl.classList.remove('capture-delayed');
    }

    this.getElemAppend().appendChild(timerEl);
  }

  static setProgressBar(countdown) {
    const wrapperEl = document.querySelector('.nsc-timer-progress');
    if (!wrapperEl) {
      return;
    }
    this.insertProgressBarHtml(wrapperEl, { timer: countdown });

    let counter = 0;
    this.progressBarTimer = setInterval(async () => {
      counter++;
      if (counter >= countdown) {
        this.hideTimer();
        nscCore.sendMessageSync({ operation: 'stopScreencastTimer' });
        setTimeout(async () => {
          nscCore.sendMessageSync({ operation: 'startScreencast' });
        }, 1000);
      }

      const text = countdown > counter ? countdown - counter : 0;
      const percent = countdown ? Math.ceil(counter / countdown * 100) : 0;
      this.updateCircleProgress(percent, text);
    }, 1000);
  }

  static drawCircleProgress(ctx, color, lineWidth, radius, percent) {
    percent = Math.min(Math.max(0, percent || 1), 1);
    ctx.beginPath();
    ctx.arc(0, 0, radius, 0, Math.PI * 2 * percent, false);
    ctx.strokeStyle = color;
    ctx.lineCap = 'square';
    ctx.lineWidth = lineWidth;
    ctx.stroke();
  }

  static updateCircleProgress(percent, timer) {
    if (!this.progressBar) {
      return null;
    }

    const { canvas, text, lineWidth, radius } = this.progressBar;
    text.textContent = timer;

    const ctx = canvas.getContext('2d');
    this.drawCircleProgress(ctx, '#3b3b3b', lineWidth, radius, 100 / 100);
    if (percent) {
      this.drawCircleProgress(ctx, '#1694ab', lineWidth, radius, percent / 100);
    }

    return this.progressBar;
  }

  static insertProgressBarHtml(wrapperEl, settings = {}) {
    if (!wrapperEl) {
      return null;
    }

    const options = {
      timer: 0,
      percent: 0,
      size: 120,
      lineWidth: 7,
      rotate: 0,
      ...settings
    };

    const { timer, percent, lineWidth, rotate, size } = options;

    const canvas = document.createElement('canvas');
    const text = document.createElement('span');
    text.textContent = options.timer;

    const ctx = canvas.getContext('2d');
    canvas.width = canvas.height = options.size;

    wrapperEl.appendChild(text);
    wrapperEl.appendChild(canvas);

    ctx.translate(size / 2, size / 2);
    ctx.rotate((-1 / 2 + rotate / 180) * Math.PI);
    const radius = (size - lineWidth) / 2;

    this.progressBar = {
      canvas,
      text,
      lineWidth,
      radius
    };

    this.updateCircleProgress(percent, timer);

    return this.progressBar;
  }

  static getElemAppend() {
    return document.fullscreenElement ? document.fullscreenElement : document.body;
  }
}
