import LS from '@/lib/LocalStorage';
import ContentHelper from '@/content/ContentHelper';
import nscExt from '@/lib/extension';

export default class ContentWatermark {
  static async init({ status }) {
    const watermarkEnable = await LS.getItem('watermarkEnable');
    const watermarkPosition = await LS.getItem('watermarkPosition');
    const watermarkEnableTime = await LS.getItem('watermarkEnableTime');
    const watermarkTime = await LS.getItem('watermarkTime');

    // const { status } = camera ? { status: true } : await nscExt.getScreencastStatus();

    // console.log('ContentWatermark Enable', watermarkEnable);

    if (status && watermarkEnable) {
      const watermark = await nscExt.getWaterMark();

      let x; let y; const
        shift = 10;
      switch (watermarkPosition) {
        case 'lt':
          // if (typeStartScreencast === 'tab-select') {
          //   x = req.cropVideoPosition.x + shift;
          //   y = req.cropVideoPosition.y + shift;
          // } else {
          x = shift;
          y = shift;
          // }
          break;
        case 'rt':
          // if (req.typeCapture === 'tab-select') {
          //   x = req.cropVideoPosition.x + req.cropVideoPosition.w - watermark.width - shift;
          //   y = req.cropVideoPosition.y + shift;
          // } else {
          x = document.documentElement.clientWidth - watermark.width - shift;
          y = shift;
          // }
          break;
        case 'lb':
          // if (req.typeCapture === 'tab-select') {
          //   x = req.cropVideoPosition.x + shift;
          //   y = req.cropVideoPosition.y + req.cropVideoPosition.h - watermark.height - shift;
          // } else {
          x = shift;
          y = document.documentElement.clientHeight - watermark.height - shift;
          // }
          break;
        case 'rb':
          // if (req.typeCapture === 'tab-select') {
          //   x = req.cropVideoPosition.x + req.cropVideoPosition.w - watermark.width - shift;
          //   y = req.cropVideoPosition.y + req.cropVideoPosition.h - watermark.height - shift;
          // } else {
          x = document.documentElement.clientWidth - watermark.width - shift;
          y = document.documentElement.clientHeight - watermark.height - shift;
          // }
          break;
        default: // case 'c':
          // if (req.typeCapture === 'tab-select') {
          //   x = Math.floor(req.cropVideoPosition.x + (req.cropVideoPosition.w - watermark.width) / 2);
          //   y = Math.floor(req.cropVideoPosition.y + (req.cropVideoPosition.h - watermark.height) / 2);
          // } else {
          x = Math.floor((document.documentElement.clientWidth - watermark.width) / 2);
          y = Math.floor((document.documentElement.clientHeight - watermark.height) / 2);
          // }
          break;
      }

      if (document.getElementById('nsc_watermark_video')) {
        const $img = document.getElementById('nsc_watermark_video');
        $img.setAttribute('src', watermark.toDataURL());
        $img.style.top = `${y}px`;
        $img.style.left = `${x}px`;
      } else {
        const $img = document.createElement('img');
        $img.id = 'nsc_watermark_video';
        $img.setAttribute('src', watermark.toDataURL());
        $img.style.pointerEvents = 'none';
        $img.style.zIndex = 9999999999999;
        $img.style.display = 'block';
        $img.style.position = 'fixed';
        $img.style.top = `${y}px`;
        $img.style.left = `${x}px`;

        document.body.appendChild($img);
      }

      if (watermarkEnableTime) {
        const { time } = await nscExt.getScreencastStatus();
        let recordTime = time || -1000; // 1 sec timeout video

        (function checkTime() {
          if (recordTime >= watermarkTime * 1000) {
            ContentHelper.deleteElementBuId('nsc_watermark_video');
          } else {
            recordTime += 100;
            window.setTimeout(checkTime, 100);
          }
        }());
      }
    } else {
      ContentHelper.deleteElementBuId('nsc_watermark_video');
    }
  }
}
