import nscCore from '@/lib/core';
import ContentHelper from '@/content/ContentHelper';

export default class ContentPopup {
  static clearEvent() {
    const nscPopup = document.querySelector('.nsc-popup');
    if (nscPopup) {
      nscPopup.remove();
    }
    // document.getElementById('nsc_form_login_nimbus').removeEventListener('mousedown', this.loginSubmit);
    // document.getElementById('nsc_form_register_nimbus').removeEventListener('mousedown', this.registerSubmit);
    // document.getElementById('nsc_form_remind_password_nimbus').removeEventListener('mousedown', this.remindSubmit);

    // $('.nsc-popup-close button').off('click', window.nscPopup.clearEvent);
    // return false;
  }

  static async get(template) {
    const { body } = await nscCore.sendMessage({
      operation: 'contentPopupRequest',
      action: 'info'
    });
    this.clearEvent();
    const html = await fetch(chrome.runtime.getURL(`assets/template/${template}.html`))
      .then(res => res.text());

    const container = document.createElement('div');
    container.innerHTML = html;
    document.body.appendChild(container);
    document.querySelector('.nsc-popup').style.position = 'fixed';

    if (body && body.login) {
      // document.getElementById('nsc_stream_welcome').querySelector('.button').removeClass('nsc-open-popup-login-nimbus');
      [...document.querySelectorAll('.nsc-hide-login')].forEach(e => e.classList.add('nsc-hide'));
      // if (response.premium.active) {
      //   $('.nsc-stream-hide-premium').addClass('nsc-hide');
      //   $('.nsc-stream-show-premium').removeClass('nsc-hide');
      // } else {
      //   $('#nsc_stream_welcome .button').addClass('nsc-open-popup-premium-nimbus');
      // }
    }
    const closeButtons = document.querySelectorAll('.nsc-popup-close button, .nsc-popup-close a, .nsc-popup-close');
    closeButtons.forEach(closeButton => {
      closeButton.addEventListener('click', () => {
        this.clearEvent();
        return false;
      });
    });

    const remindButton = document.querySelector('.nsc-open-popup-remind-pass-nimbus');
    if (remindButton) {
      remindButton.addEventListener('click', () => {
        this.clearEvent();
        this.remindOpen();
      });
    }

    const registerButton = document.querySelector('.nsc-open-popup-register-nimbus');
    if (registerButton) {
      registerButton.addEventListener('click', () => {
        this.clearEvent();
        const param = `capture-login-success=1&sr=${ContentHelper.is_firefox ? 'screens_firefox' : 'screens_chrome'}&sc=1`;
        window.open(`https://nimbusweb.me/auth/?f=register&b=capture&hpts=1&success=/auth/t/s.html?${param}`, '_blank');
        return false;
      });
    }

    const loginButton = document.querySelector('.nsc-open-popup-login-nimbus');
    if (loginButton) {
      loginButton.addEventListener('click', () => {
        this.clearEvent();
        this.loginOpen();
      });
    }

    const premiumButton = document.querySelector('.nsc-open-popup-premium-nimbus');
    if (premiumButton) {
      premiumButton.addEventListener('click', () => {
        this.clearEvent();
        this.premiumOpen();
      });
    }

    // $('.nsc-open-media-access').on('click', () => {
    //   chrome.runtime.sendMessage({ operation: 'content_media_access', access: true });
    //   this.clearEvent();
    // });

    const i18nElement = document.querySelectorAll('.nsc-popup *[data-i18n]');

    i18nElement.forEach(elem => {
      const text = elem.getAttribute('data-i18n');
      const attr = elem.getAttribute('data-i18n-attr');
      const message = chrome.i18n.getMessage(text);

      if (attr && text) {
        elem.setAttribute(attr, message);
      } else if (text) {
        elem.innerHTML = message;
      }
    });
  }

  static async loginOpen() {
    await this.get('popup-login');
    document.getElementById('nsc_form_login_nimbus')
      .addEventListener('submit', e => { this.loginSubmit(e); });

    document.getElementById('nsc_connect_to_google')
      .addEventListener('click', () => {
        this.clearEvent();
        window.open('https://nimbusweb.me/auth/openidconnect.php?env=app&provider=google', '_blank');
        return false;
      });

    document.getElementById('nsc_connect_to_facebook')
      .addEventListener('click', () => {
        this.clearEvent();
        window.open('https://nimbusweb.me/auth/openidconnect.php?env=app&provider=facebook', '_blank');
        return false;
      });
  }

  static async loginSubmit(e) {
    e.preventDefault();

    const {
      email,
      password
    } = e.currentTarget.elements;

    if (password.value.length < 8) {
      password.classList.add('wrong');
    } else if (!/\S+@\S+\.\S+/.test(email.value)) {
      email.classList.add('wrong');
    } else {
      const {
        errorCode,
        body
      } = await nscCore.sendMessage({
        operation: 'contentPopupRequest',
        action: 'auth',
        login: email.value,
        password: password.value
      });
      if (errorCode === 0) {
        this.clearEvent();
      } else if (errorCode === -26) {
        this.clearEvent();
        await this.challengeOpen(body);
      } else {
        alert(chrome.i18n.getMessage('notificationLoginFail'));
      }
    }
  }

  static async challengeOpen(body) {
    await this.get('popup-login-challenge');

    const imgData = body.challenge.type === 'otp' ? '' : `data:image/png;base64,${body.challenge.image}`;

    const connectNimbusChallenge = document.getElementById('nsc_popup_connect_nimbus_challenge');
    connectNimbusChallenge.addEventListener('submit', e => { this.challengeSubmit(e); });

    connectNimbusChallenge.querySelector('input[name=state]').value = body.challenge.state;
    connectNimbusChallenge.querySelector('img')
      .setAttribute('src', imgData);
  }

  static async challengeSubmit(e) {
    e.preventDefault();

    const {
      state,
      code
    } = e.currentTarget.elements;

    if (code.value.length < 0) {
      code.classList.add('wrong');
    } else {
      const { errorCode } = await nscCore.sendMessage({
        operation: 'contentPopupRequest',
        action: 'challenge',
        state: state.value,
        code: code.value
      });
      if (errorCode === 0) {
        this.clearEvent();
        await this.remindOpen();
      } else {
        // $.ambiance({ message: chrome.i18n.getMessage('notificationLoginFail'), type: 'error', timeout: 5 });
      }
    }
  }

  static async remindOpen() {
    await this.get('popup-remind');
    document.getElementById('nsc_form_remind_password_nimbus')
      .addEventListener('submit', e => { this.remindSubmit(e); });
  }

  static async remindSubmit(e) {
    e.preventDefault();

    const { email } = e.currentTarget.elements;

    if (!/\S+@\S+\.\S+/.test(email.value)) {
      email.classList.add('wrong');
    } else {
      const { errorCode } = await nscCore.sendMessage({
        operation: 'contentPopupRequest',
        action: 'remind',
        email: email.value
      });
      if (errorCode === 0) {
        this.clearEvent();
        await this.loginOpen();
        alert(chrome.i18n.getMessage('notificationRestoreSent'));
      } else {
        this.clearEvent();
      }
    }
  }

  static async registerOpen() {
    await this.get('popup-register');
    document.getElementById('#nsc_form_register_nimbus')
      .addEventListener('submit', e => { this.registerSubmit(e); });
  }

  static async registerSubmit(e) {
    const {
      email,
      password,
      passrepeat
    } = e.currentTarget.elements;

    if (password.value.length < 8) {
      password.classList.add('wrong');
    } else if (password.value !== passrepeat.value) {
      password.classList.add('wrong');
      passrepeat.classList.add('wrong');
    } else if (!/\S+@\S+\.\S+/.test(email.value)) {
      email.classList.add('wrong');
    } else {
      const { errorCode } = await nscCore.sendMessage({
        operation: 'content_popup_request',
        action: 'register',
        email: email.value,
        password: password.value
      });

      if (errorCode === 0) {
        // eslint-disable-next-line no-shadow
        const { errorCode } = await nscCore.sendMessage({
          operation: 'content_popup_request',
          action: 'auth',
          email: email.value,
          password: password.value
        });
        if (errorCode === 0) {
          this.clearEvent();
          const isMediaAccess = await nscCore.sendMessage({ operation: 'get_is_media_access' });
          await this.streamStartOpen();
          document.querySelector('.nsc-popup-actions-text-registering')
            .classList
            .remove('nsc-hide');
          if (!isMediaAccess) {
            document.querySelector('.nsc-popup-actions-text-media-access')
              .classList
              .remove('nsc-hide');
            document.querySelector('.nsc-popup-form-media-access-button')
              .classList
              .remove('nsc-hide');
          } else {
            document.querySelector('.nsc-popup-form-default')
              .classList
              .remove('nsc-hide');
          }
        } else {
          this.clearEvent();
          // $.ambiance({ message: chrome.i18n.getMessage('notificationRegisterFail'), type: 'error', timeout: 5 });
        }
      } else if (errorCode === -4) {
        this.clearEvent();
        // $.ambiance({ message: chrome.i18n.getMessage('notificationEmailFail'), type: 'error', timeout: 5 });
      } else {
        this.clearEvent();
        // $.ambiance({ message: chrome.i18n.getMessage('notificationRegisterFail'), type: 'error', timeout: 5 });
      }
    }
    return false;
  }

  static async premiumOpen({ analytics }) {
    await this.get('popup-premium');
    const link = document.querySelector('.nsc-popup-form-actions a');
    const href = link.getAttribute('href');
    link.setAttribute('href', href.replace(/utm_campaign[^&]+/, `utm_campaign=${analytics}`));
  }

  static async streamStartOpen() {
    await this.get('popup-stream-start');
  }
}
