  static is_mobile = /(Android|iPhone|iPod|iPad)/i.test(navigator.userAgent);
  static is_windows = ['Win32', 'Win64', 'Windows', 'WinCE'].indexOf(navigator.platform) !== -1;

  static is_linux = (!this.is_chrome_os || !this.is_macintosh)
