// eslint-disable-next-line import/no-extraneous-dependencies
import moment from 'moment';

export default class nscExt {
  static trialVideoDuration = 60 * 1000 * 5;
  static trialTime = 7 * 24 * 3600 * 1000;
  static welcomePageUrl = 'https://nimbusweb.me/capture-new-wizard/?utm_source=capture&utm_medium=addon&utm_campaign=welcom_page';

  static async enableSaveAs() {
    const value = await LS.getItem('enableSaveAs');
    return value && value !== 'false';
  }

  static async imageFormat() {
    return (await LS.getItem('imageFormat')) === 'jpg' ? 'jpeg' : 'png';
  }

  static async imageQuality() {
    return (await LS.getItem('imageQuality')) || 100;
  }

  static async pageInfo() {
    return (await LS.getItem('pageInfo')) || {};
  }

  static async imageFileInfo() {
    return (await LS.getItem('imageFileInfo', true)) || {};
  }

  static async imageFileParts() {
    return (await LS.getItem('imageFileParts', true)) || [];
  }

  static generateGUID(length = 32) {
    const chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXTZabcdefghiklmnopqrstuvwxyz';
    let GUID = '';

    for (let i = 0; i < length; i++) {
      const rNum = Math.floor(Math.random() * chars.length);
      GUID += chars.substring(rNum, rNum + 1);
    }
    return GUID;
  }

  static async getScreencastStatus() {
    const { status, state, time } = await nscCore.sendMessage({ operation: 'getScreencastStatusGlobal' });
    return { status, state, time };
  }

  static async setExtensionBadge(countdown) {
    await chrome.action.disable();
    if (countdown > 0) {
      await nscCore.setTimeout(1000);
      await chrome.action.setBadgeText({ text: countdown.toString() });
      await chrome.action.setBadgeBackgroundColor({ color: '#000' });
      await this.setExtensionBadge(--countdown);
    } else {
      await chrome.action.enable();
      await chrome.action.setBadgeText({ text: '' });
    }
  }

  static formatBytes(bytes, decimals = 2) {
    if (!+bytes) return '0 Bytes';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    // eslint-disable-next-line no-mixed-operators
    return `${parseFloat((bytes / k ** i).toFixed(dm))} ${sizes[i]}`;
  }

  static async openPage(url) {
    console.log('openPage in tab', url);
    await chrome.tabs.create({ url });
    // await nscCore.sendMessage({ operation: "open_page", url: url });
  }

  static async captureVisibleTab() {
    const { windowId = null } = await nscExt.pageInfo();
    const format = await nscExt.imageFormat();
    const quality = await nscExt.imageQuality();
    return new Promise(resolve => {
      chrome.tabs.captureVisibleTab(windowId, { format, quality }, resolve);
    });
  }

  static async openEditPage(editPageRouter) {
    await LS.setItem('EditPageRouter', editPageRouter);
    await chrome.tabs.create({ url: 'edit.html' });
  }

  static dataURItoBlob(dataURI) {
    // eslint-disable-next-line prefer-destructuring
    const mime = dataURI.split(',')[0].split(':')[1].split(';')[0];
    const binary = atob(dataURI.split(',')[1]);
    const array = [];
    for (let i = 0; i < binary.length; i++) {
      array.push(binary.charCodeAt(i));
    }
    return new Blob([new Uint8Array(array)], { type: mime });
  }

  static arrayBufferToDataURI(buffer) {
    let binary = '';
    const bytes = new Uint8Array(buffer);
    const len = bytes.byteLength;
    for (let i = 0; i < len; i++) {
      binary += String.fromCharCode(bytes[i]);
    }
    return window.btoa(binary);
  }

  /** @description Onload file and convert to blob.
   * @param {string} url to file.
   * @return {Blob} Blob
   */

  static async urlToArrayBuffer(url) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();

      xhr.open('GET', url, true);
      xhr.responseType = 'arraybuffer';
      xhr.onload = function onload() {
        if (this.status === 200) {
          resolve(new Uint8Array(this.response));
        } else {
          reject(new Error('arrayBuffer status != 200'));
        }
      };
      xhr.onerror = reject;
      xhr.send();
    });
  }

  static async blobToDataUrl(blob) {
    return new Promise(resolve => {
      const a = new FileReader();

      a.onload = function onload(e) {
        resolve(e.target.result);
      };
      a.readAsDataURL(blob);
    });
  }

  /** @description onLoad image.
   * @param {string} url image.
   * @return {HTMLImageElement} image element
   */

  static async imageLoad(url) {
    return new Promise((resolve, reject) => {
      const image = new Image();

      image.onload = function onload() {
        resolve(image);
      };
      image.onerror = function onerror(e) {
        // Logger.error(`Error load image ${url}`);
        reject(e);
      };
      image.src = url;
    });
  }

  static async canvasToBlob(canvas, type = 'image/png', quality = 1) {
    return new Promise(resolve => {
      canvas.toBlob(blob => {
        resolve(blob);
      }, type, quality);
    });
  }

  /** @description get watermark image.
   * @return {HTMLCanvasElement} canvas element
   */

  static async getWaterMark(video) {
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async (resolve, reject) => {
      const c = document.createElement('canvas');
      const ctx = c.getContext('2d');
      const watermarkType = await LS.getItem('watermarkType');
      const watermarkFileData = await LS.getItem('watermarkFile', true);

      if (watermarkType === 'image') {
        const watermark = new Image();

        watermark.onload = async () => {
          const watermarkPercent = await LS.getItem('watermarkPercent');
          const watermarkAlpha = await LS.getItem('watermarkAlpha');
          const width = watermark.width * watermarkPercent;
          const height = watermark.height * watermarkPercent;

          c.width = width;
          c.height = height;

          ctx.globalAlpha = +watermarkAlpha;
          ctx.drawImage(watermark, 0, 0, width, height);
          resolve(c);
        };
        watermark.onerror = reject;
        watermark.src = watermarkFileData;
      } else {
        const watermarkSize = await LS.getItem('watermarkSize');
        const watermarkFont = await LS.getItem('watermarkFont');
        const watermarkText = await LS.getItem('watermarkText');
        const watermarkAlpha = await LS.getItem('watermarkAlpha');
        const watermarkColor = await LS.getItem('watermarkColor');
        await this.checkFontLoad(watermarkFont);
        const fontCss = `bold ${watermarkSize}px ${watermarkFont}`;
        const text = await this.replaceWatermarkText(watermarkText);
        const fontSize = this.sizeFont({ text, fontCss });
        c.width = fontSize.w;
        c.height = fontSize.h;
        ctx.textBaseline = 'hanging';
        ctx.textAlign = 'left';
        ctx.globalAlpha = +watermarkAlpha;
        if (video) {
          // ctx.fillStyle = inversion(localStorage.watermarkColor);
          ctx.fillRect(0, 0, fontSize.w, fontSize.h);
        }
        ctx.fillStyle = watermarkColor;
        ctx.font = fontCss;
        ctx.fillText(text, 0, 0, fontSize.w);
        resolve(c);
      }
    });
  }

  static async getWaterMarkPosition(watermark, canvas) {
    const watermarkPosition = await LS.getItem('watermarkPosition');
    let x;
    let y;
    const shift = 10;
    // eslint-disable-next-line default-case
    switch (watermarkPosition) {
      case 'lt':
        x = shift;
        y = shift;
        break;
      case 'rt':
        x = canvas.width - watermark.width - shift;
        y = shift;
        break;
      case 'lb':
        x = shift;
        y = canvas.height - watermark.height - shift;
        break;
      case 'rb':
        x = canvas.width - watermark.width - shift;
        y = canvas.height - watermark.height - shift;
        break;
      case 'c':
        x = Math.floor((canvas.width - watermark.width) / 2);
        y = Math.floor((canvas.height - watermark.height) / 2);
        break;
    }
    return { x, y };
  }

  static async checkFontLoad(fond) {
    const fondList = {
      BadScript: 'fonts/BadScriptRegular.woff',
      Lobster: 'fonts/lobsterRegular.woff',
      MarckScript: 'fonts/MarckScriptRegular.woff',
      PoiretOne: 'fonts/PoiretOneNormal.woff',
      RobotoSlab: 'fonts/RobotoSlabRegular.woff',
      RussoOne: 'fonts/RussoOneRegular.woff'
    };

    if (fondList[fond]) {
      const url = chrome.runtime.getURL(fondList[fond]);
      const myFont = new FontFace(fond, `url('${url}')`, {
        style: 'normal',
        weight: '400'
      });

      await myFont.load();
      console.info('Font loaded', fond);
      document.fonts.add(myFont);
    } else {
      console.error('Font no loaded');
    }
  }

  static async createCanvasParts(info, format, parts) {
    console.log('createFullImage', info, format, parts);
    // eslint-disable-next-line no-underscore-dangle
    const _self = this;
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async resolve => {
      let isSetSize = false;
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');

      canvas.width = Math.ceil(info.w * info.z);
      canvas.height = Math.ceil(info.h * info.z);

      for (let index = 0; index < info.parts.length; index++) {
        const part = info.parts[index];

        try {
          // eslint-disable-next-line no-await-in-loop
          const image = await _self.imageLoad(parts[index]);

          if (
            part.x2 !== undefined
            && part.y2 !== undefined
            && part.w2 !== undefined
            && part.h2 !== undefined
          ) {
            if (!isSetSize) {
              if (image.width > canvas.width && part.x === 0) {
                info.z = +(image.width / canvas.width).toFixed(2);
              }
              if (info.isMobile) {
                info.z = 1;
              }
              canvas.width = Math.ceil(info.w * info.z);
              canvas.height = Math.ceil(info.h * info.z);
              isSetSize = true;
            }

            if (index === info.parts.length - 1 && part.y2 + part.h2 > info.h) {
              // конец страницы, не доскролл
              part.y2 = info.h - part.h2;
            }
            ctx.drawImage(
              image,
              Math.ceil(part.x * info.z),
              Math.ceil(part.y * info.z),
              Math.ceil(part.w * info.z),
              Math.ceil(part.h * info.z),
              Math.ceil(part.x2 * info.z),
              Math.ceil(part.y2 * info.z),
              Math.ceil(part.w2 * info.z),
              Math.ceil(part.h2 * info.z)
            );
          } else {
            if (canvas.width > image.width) {
              canvas.width = image.width;
            }
            if (canvas.height > image.height) {
              canvas.height = image.height;
            }

            ctx.drawImage(
              image,
              Math.ceil(-part.x * info.z),
              Math.ceil(-part.y * info.z),
              image.width,
              image.height
            );
          }
          if (index === info.parts.length - 1) {
            const dataURL = canvas.toDataURL(`image/${format}`);

            canvas.toBlob(
              blob => {
                resolve({ canvas, dataURL, blob });
              },
              `image/${format}`,
              1
            );
          }
        } catch (e) {
          console.error(e);
        }
      }
    });
  }

  static sizeFont(data) {
    const dummy = document.createElement('div');

    dummy.appendChild(document.createTextNode(data.text));
    dummy.setAttribute(
      'style',
      `font: ${data.fontCss}; line-height: normal; float: left; white-space: nowrap; overflow: hidden;`
    );
    document.body.appendChild(dummy);
    const result = { w: dummy.offsetWidth, h: dummy.offsetHeight };
    document.body.removeChild(dummy);
    return result;
  }

  static getHostInUrl(url) {
    const { host } = new URL(url);
    return host;
  }

  static async replaceWatermarkText(text) {
    const { url, title, time } = await LS.getItem('pageInfo');
    const domain = this.getHostInUrl(url);
    // eslint-disable-next-line prefer-destructuring
    let date = time.split(' ')[0];
    let timeStamp = time.split(' ')[1] || '';

    if (window.navigator.language === 'ru') {
      const dateSplit = date.split('.');
      date = `${dateSplit[2]}.${dateSplit[1]}.${dateSplit[0]}`;
    } else {
      const dateSplit = date.split('.');
      date = `${dateSplit[1]}.${dateSplit[2]}.${dateSplit[0]}`;

      timeStamp = timeStamp.split(':');
      timeStamp[2] = +timeStamp[0] < 12 ? `${timeStamp[2]}AM` : `${timeStamp[2]}PM`;
      timeStamp[0] = +timeStamp[0] % 12 || 12;
      timeStamp = timeStamp.join(':');
    }

    return text.replace(/\{url}/, url || '')
      .replace(/\{title}/, title || '')
      .replace(/\{domain}/, domain || '')
      .replace(/\{date}/, date)
      .replace(/\{time}/, timeStamp)
      .replace(/\{ms}/, time.split(' ')[2] || '')
      .replace(/\{timestamp}/, time.split(' ')[3] || '');
  }

  static async replaceFileName(pattern, info = {}) {
    const pageInfo = await LS.getItem('pageInfo');
    const { url, title, time } = { ...info, ...pageInfo };
    const domain = this.getHostInUrl(url);

    pattern = pattern
      .replace(/\{url}/, url || '')
      .replace(/\{title}/, title || '')
      .replace(/\{domain}/, domain || '')
      .replace(/\{date}/, time.split(' ')[0] || '')
      .replace(/\{time}/, time.split(' ')[1] || '')
      .replace(/\{ms}/, time.split(' ')[2] || '')
      .replace(/\{timestamp}/, time.split(' ')[3] || '');

    return pattern.replace(/[*|\\:."<>?/#]+/gi, '_');
  }

  static async getFileNameVideo(format, info) {
    const fileNamePatternScreencast = await LS.getItem('fileNamePatternScreencast');
    const fileName = await this.replaceFileName(
      fileNamePatternScreencast,
      info
    );

    return fileName + (format ? `.${format}` : '');
  }

  static async getFileNameImage(format, info) {
    const fileNamePatternScreenshot = await LS.getItem('fileNamePatternScreenshot');
    const fileName = await this.replaceFileName(
      fileNamePatternScreenshot,
      info
    );

    return fileName + (format ? `.${format}` : '');
  }

  static getTimeStamp() {
    const time = new Date();
    const y = time.getFullYear();
    let m; let d; let h; let M; let s; let mm;
    const timeStamp = Date.now();

    m = time.getMonth() + 1;
    d = time.getDate();
    h = time.getHours();
    M = time.getMinutes();
    s = time.getSeconds();
    mm = time.getMilliseconds();
    if (m < 10) m = `0${m}`;
    if (d < 10) d = `0${d}`;
    if (h < 10) h = `0${h}`;
    if (M < 10) M = `0${M}`;
    if (s < 10) s = `0${s}`;
    if (mm < 10) mm = `00${mm}`;
    else if (mm < 100) mm = `0${mm}`;
    return (`${y}.${m}.${d} ${h}:${M}:${s} ${mm} ${timeStamp}`);
  }

  static async createPageInfo() {
    const tab = await nscCore.tabActive();
    let info = {
      id: 1,
      windowId: 0,
      url: 'https://nimbusweb.me/',
      title: 'nimbus-capture',
      time: this.getTimeStamp()
    };
    if (tab) {
      info = {
        id: tab.id,
        windowId: tab.windowId,
        url: tab.url,
        title: tab.title,
        time: this.getTimeStamp()
      };
    }

    LS.setItem('pageInfo', JSON.stringify(info));

    return info;
  }

  static async getTrial() {
    const firstTime = await LS.getItem('firstTime');
    const isTrial = await LS.getItem('isTrial');
    const now = moment(firstTime + this.trialTime);
    const end = moment();
    const duration = moment.duration(now.diff(end));

    return isTrial && duration.asMilliseconds() <= 0;
  }

  static async streamCrop(stream, { w, h, x, y, innerWidth, innerHeight }) {
    let timeoutId = null;
    let imageStream = new ImageCapture(stream.getVideoTracks()[0]);
    let canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    const { width, height } = await imageStream.grabFrame();
    const z = width / innerWidth;
    canvas.width = Math.ceil(w * z);
    canvas.height = Math.ceil(h * z);
    const canvasStream = canvas.captureStream();
    const audioStreamTracks = stream.getAudioTracks();
    const videoStreamTracks = canvasStream.getVideoTracks();
    console.log(`Stream Crop ${w} ${h} ${x} ${y} ${z} ${innerWidth} ${innerHeight} ${width} ${height}`);

    const grabFrame = timeout => {
      clearTimeout(timeoutId);
      timeoutId = window.setTimeout(async () => {
        try {
          const image = await imageStream.grabFrame();
          ctx.drawImage(image, -Math.ceil(x * z), -Math.ceil(y * z));
        } catch (e) {
          // console.log('Stream Crop not frame');
        }
        grabFrame(35);
      }, timeout);
    };

    grabFrame(35);

    const outputStream = new MediaStream([...videoStreamTracks, ...audioStreamTracks]);

    const stop = () => {
      console.log('Stream Crop stop');
      imageStream = null;
      canvas = null;
      clearTimeout(timeoutId);
      // eslint-disable-next-line no-unused-expressions
      if (outputStream) {
        outputStream.getTracks()
          .forEach(track => {
            track.stop();
          });
      }

      if (stream) {
        stream.getTracks()
          .forEach(track => {
            track.stop();
          });
      }
    };

    return {
      outputStream,
      stop
    };
  }
}
