import nscCore from '@/lib/core';
import ContentCropper from '@/content/ContentCropper';
import LS from '@/lib/LocalStorage';

class ScreencastTabSelect {
  static active = false;

  static async init() {
    this.active = true;

    ContentCropper.setOptions({ wrapperFull: false, scrollEnable: false, videoCrop: true });
    ContentCropper.mounted();
  }
}

ContentCropper.on('destroy', () => {
  if (!ScreencastTabSelect.active) return;
  ScreencastTabSelect.active = false;
});

ContentCropper.on('end', async () => {
  if (!ScreencastTabSelect.active) return;
  const z = window.devicePixelRatio;
  const { innerWidth, innerHeight } = window;
  await LS.setItem('videoCropPosition', { z, ...ContentCropper.getFinishPoints(), innerWidth, innerHeight });
});

ContentCropper.on('action', async afterAction => {
  if (!ScreencastTabSelect.active) return;

  switch (afterAction) {
    case 'record': {
      const videoCountdown = await LS.getItem('videoCountdown');

      if (videoCountdown) {
        nscCore.sendMessageSync({ operation: 'startScreencastTimer' });
      } else {
        await nscCore.sendMessage({ operation: 'startScreencast' });
      }
      break;
    }

    default:
      break;
  }
});

export default ScreencastTabSelect;
