import nscCore from '@/lib/core';
import ContentHelper from '@/content/ContentHelper';
import LS from '@/lib/LocalStorage';
import ContentCropper from '@/content/ContentCropper';
import ContentVideoPanel from '@/content/ContentVideoPanel';

export default class ActionFragment {
  static position = {};
  static elem = null;
  static elems = [];
  static appType = null;
  static blurVideoPanel = false;

  static savePosition() {
    LS.setItem('fragmentPosition', this.position);
  }

  static eventStop = e => {
    if (this.blurVideoPanel) {
      ContentVideoPanel.drawBlurArea(this.position);
      ContentVideoPanel.drawEnd(null);
    } else {
      this.viewButton(e);
    }

    e.preventDefault();
    e.stopPropagation();
    e.stopImmediatePropagation();
  };

  static eventClear() {
    this.eventElements();

    document.removeEventListener('contextmenu', this.eventContextMenu);
    document.removeEventListener('mousemove', this.viewBorder);
  }

  static eventElements(elem) {
    if (elem) {
      if (!elem.classList.contains('nsc-panel-compact') || !elem.closest('.nsc-panel-compact')) {
        elem.addEventListener('click', this.eventStop);
        this.elems.push(elem);

        // eslint-disable-next-line no-restricted-syntax
        for (const child of elem.children) {
          this.eventElements(child);
        }
      }
    } else {
      // eslint-disable-next-line no-restricted-syntax
      for (const child of this.elems) {
        child.removeEventListener('click', this.eventStop);
      }

      this.elems = [];
    }
  }

  static eventContextMenu = e => {
    this.viewClear();
    this.eventClear();
    this.eventStop(e);
  };

  static viewClear() {
    ContentHelper.deleteElementBuId('nscCaptureFragmentBox');
  }

  static viewBorder = e => {
    this.elem = e.target;

    if (this.elem.classList.contains('nsc-capture-fragment-box')) {
      return;
    }

    const { width, height, left, top } = this.elem.getBoundingClientRect();
    this.position = { x: parseFloat(window.scrollX + left), y: parseFloat(window.scrollY + top), w: width, h: height };
    if (!document.getElementsByClassName('nsc-capture-fragment-box').length) {
      const div = document.createElement('div');

      div.className = 'nsc-capture-fragment-box';
      div.id = 'nscCaptureFragmentBox';

      document.body.appendChild(div);
    }

    const nscCaptureFragmentBox = document.getElementById('nscCaptureFragmentBox');

    nscCaptureFragmentBox.style.top = `${this.position.y}px`;
    nscCaptureFragmentBox.style.left = `${this.position.x}px`;
    nscCaptureFragmentBox.style.width = `${this.position.w}px`;
    nscCaptureFragmentBox.style.height = `${this.position.h}px`;
  };

  static viewButton() {
    document.removeEventListener('contextmenu', this.eventContextMenu);
    document.removeEventListener('mousemove', this.viewBorder);

    if (!document.querySelector('.nsc-cropper-buttons')) {
      const screenshotFragmentFinish = afterAction => {
        this.eventClear();
        this.viewClear();
        nscCore.sendMessageSync({ operation: 'screenshotFragmentFinish', afterAction });
      };

      const cropButtons = ContentCropper.createElement('buttons');

      const buttonEdit = ContentCropper.createButton('edit', chrome.i18n.getMessage('cropBtnEdit'), screenshotFragmentFinish);
      const buttonSave = ContentCropper.createButton('download', chrome.i18n.getMessage('cropBtnSave'), screenshotFragmentFinish);
      const buttonCancel = ContentCropper.createButton(
        'cancel',
        chrome.i18n.getMessage('cropBtnCancel'),
        () => { this.eventClear(); this.viewClear(); }
      );

      const cropMore = ContentCropper.createElement('more');
      const moreButton = ContentCropper.createButton('more');
      const nsMoreContainer = ContentCropper.createElement('more-container');

      const nsCropButtonNimbus = ContentCropper.createButton('nimbus', 'Nimbus', screenshotFragmentFinish);
      const nsCropButtonSlack = ContentCropper.createButton('slack', chrome.i18n.getMessage('editBtnSlack'), screenshotFragmentFinish);
      const nsCropButtonGoogle = ContentCropper.createButton('google', chrome.i18n.getMessage('editBtnDrive'), screenshotFragmentFinish);
      // const nsCropButtonQuick = ContentCropper.createButton('quick', chrome.i18n.getMessage('editBtnQuickUpload'), screenshotFragmentFinish);
      const nsCropButtonPrint = ContentCropper.createButton('print', chrome.i18n.getMessage('editBtnPrint'), screenshotFragmentFinish);
      const nsCropButtonPdf = ContentCropper.createButton('pdf', chrome.i18n.getMessage('cropBtnSavePdf'), screenshotFragmentFinish);

      const { w, h, y } = this.position;

      if (h + y + 80 > window.innerHeight) {
        cropButtons.classList.add('inside');
      } else {
        cropButtons.classList.remove('inside');
      }

      if (w < 350 || h + y + 80 > window.innerHeight) {
        cropMore.classList.add('inside');
      } else {
        cropMore.classList.remove('inside');
      }

      nsMoreContainer.appendChild(nsCropButtonNimbus);
      nsMoreContainer.appendChild(nsCropButtonSlack);
      nsMoreContainer.appendChild(nsCropButtonGoogle);
      // nsMoreContainer.appendChild(nsCropButtonQuick);
      nsMoreContainer.appendChild(nsCropButtonPrint);
      nsMoreContainer.appendChild(nsCropButtonPdf);

      cropButtons.appendChild(buttonEdit);
      cropButtons.appendChild(buttonSave);
      cropButtons.appendChild(buttonCancel);

      cropMore.appendChild(moreButton);
      cropMore.appendChild(nsMoreContainer);

      const nscCaptureFragmentBox = document.getElementById('nscCaptureFragmentBox');

      if (nscCaptureFragmentBox) {
        nscCaptureFragmentBox.appendChild(cropButtons);
        nscCaptureFragmentBox.appendChild(cropMore);
        nscCaptureFragmentBox.classList.add('nsc-select');
      }

      this.savePosition();
      ContentHelper.pageScroll(0, y + h - window.innerHeight + 100).then(() => null);
    }
  }

  static init() {
    this.eventElements(document.body);

    document.addEventListener('contextmenu', this.eventContextMenu);
    document.addEventListener('mousemove', this.viewBorder);
  }
}
