import logger from '@/lib/logger';
// eslint-disable-next-line import/no-cycle
import LS from '@/lib/LocalStorage';
import nscExt from '@/lib/extension';
import axios from 'axios';

const headers = {
  'Content-Type': 'application/json',
  'x-client-software': 'screens_chrome',
  'x-api-version': 5,
  Accept: 'application/json'
};

export default class NimbusConnect {
  static dev = false;
  static log = true;
  static pending = false;
  static abort = false;

  static url = {
    sync: 'https://sync.nimbusweb.me',
    nimbus: 'https://nimbusweb.me'
  };

  /** @description organization select.
   * @return {string}
   */

  static async orgSelect(org) {
    if (org) {
      LS.setItem('nimbusOrganizationSelect', org);
    }
    return LS.getItem('nimbusOrganizationSelect');
  }

  /** @description workspace select.
   * @return {string}
   */

  static async workSelect(work) {
    if (work) {
      LS.setItem('nimbusWorkspaceSelect', work);
    }
    return LS.getItem('nimbusWorkspaceSelect');
  }

  /** @description note select.
   * @return {string}
   */

  static async folderSelect(folder) {
    if (folder) {
      LS.setItem('nimbusFolderSelect', folder);
    }
    return LS.getItem('nimbusFolderSelect');
  }

  /** @description get short url used dantist_api.
   * @param params Information send to server.
   * @param {string} params.url url
   * @param {string} params.domain? domain.
   * @return {string} short url
   */

  static async shortUrl(params) {
    const { url, domain } = params;
    const nimbusAutoShortUrl = await LS.getItem('nimbusAutoShortUrl');
    if (nimbusAutoShortUrl) {
      const formData = new FormData();

      formData.append('url', url);
      if (domain) {
        formData.append('domain', domain);
      }

      try {
        const { data } = await axios.post('https://nimb.ws/dantist_api.php', formData);
        // eslint-disable-next-line camelcase
        const { short_url } = data;

        // eslint-disable-next-line camelcase
        return short_url;
      } catch (error) {
        logger.error(`Connect dantist_api error, return ${url}`);

        return url;
      }
    }
    return url;
  }

  static async requestApi(action, params) {
    this.pending = true;

    const nimbusSessionId = await LS.getItem('nimbusSessionId');

    const response = await fetch(
      `${this.url.nimbus}/auth/api/${action}`,
      {
        credentials: 'include',
        method: 'post',
        headers: nimbusSessionId ? { ...headers, 'EverHelper-Session-ID': nimbusSessionId } : headers,
        body: JSON.stringify({ ...params, service: 'nimbus', languages: [navigator.language] })
      }
    );
    const json = await response.json();
    if (json.body && json.body.sessionid) {
      LS.setItem('nimbusSessionId', json.body.sessionid);
    }
    this.pending = false;
    return json;
  }

  static async requestSync(params) {
    this.pending = true;

    const nimbusSessionId = await LS.getItem('nimbusSessionId');

    const response = await fetch(
      this.url.sync,
      {
        credentials: 'include',
        method: 'post',
        headers: nimbusSessionId ? { ...headers, 'EverHelper-Session-ID': nimbusSessionId } : headers,
        body: JSON.stringify({ ...params, languages: [navigator.language], withCredentials: true })
      }
    );

    this.pending = false;
    if (this.abort) {
      this.abort = false;
      throw new Error('abort');
    } else {
      const json = await response.json();
      if (json.body && json.body.sessionid) {
        LS.setItem('nimbusSessionId', json.body.sessionid);
      }

      return json;
    }
  }

  /** @description Check premium capture.
   * @return {Promise<boolean>} is premium
   */

  static async checkPremium() {
    const globalId = await LS.getItem('appGlobalId');
    const nimbusSessionId = await LS.getItem('nimbusSessionId');
    const response = await fetch(
      'https://capture-pro.nimbusweb.me/v1/premium',
      {
        credentials: 'include',
        method: 'post',
        headers: nimbusSessionId ? { ...headers, 'EverHelper-Session-ID': nimbusSessionId } : headers,
        body: JSON.stringify({ device: { globalId } })
      }
    );

    const { state, reason } = await response.json();
    return state === 'active' ? true : reason === 'devicequota';
  }

  static async quick({ blob, comment = '', title = '' }) {
    this.pending = true;

    const nimbusSessionId = await LS.getItem('nimbusSessionId');
    const pageInfo = await LS.getItem('pageInfo');

    const fd = new FormData();
    fd.append('title', title || '');
    fd.append('commentText', comment);
    fd.append('url', pageInfo.url);
    fd.append('file', blob, title);

    const response = await fetch(
      this.url.nimbus,
      {
        credentials: 'include',
        method: 'post',
        headers: nimbusSessionId ? { 'EverHelper-Session-ID': nimbusSessionId } : {},
        body: fd
      }
    );

    this.pending = false;
    if (this.abort) {
      this.abort = false;
      throw new Error('abort');
    } else {
      const data = await response.json();
      const { errorCode, body } = data;
      if (errorCode === 0) {
        return body;
      }

      throw new Error(errorCode);
    }
  }

  static async fileUpload({ blob, name = 'file', type = 'file' }) {
    this.pending = true;

    const nimbusSessionId = await LS.getItem('nimbusSessionId');
    const fd = new FormData();
    fd.append(type, blob, name);

    const response = await fetch(
      `${this.url.sync}/files:preupload`,
      {
        credentials: 'include',
        method: 'post',
        headers: nimbusSessionId ? { 'EverHelper-Session-ID': nimbusSessionId } : {},
        body: fd
      }
    );

    this.pending = false;
    const data = await response.json();
    const { errorCode, body } = data;
    if (errorCode === 0) {
      return body;
    }

    throw new Error(errorCode);
  }

  static async notesAddBookmark({ noteId, workspaceId, src = '', icon = '', description = '', name = '' }) {
    const { body } = await this.requestSync({
      action: 'notes:addBlots',
      body: {
        workspaceId,
        globalId: noteId,
        addBlotDatas: [{
          parent: 'scroll',
          blotName: 'bookmark',
          blotParams: {
            icon,
            description,
            name,
            src,
            viewMode: 'tile'
          },
          insertAfter: 'last'
        }]
      }

    });
    return body;
  }

  static async notesAddHint({ noteId, workspaceId, text = '' }) {
    const { body } = await this.requestSync({
      action: 'notes:addBlots',
      body: {
        workspaceId,
        globalId: noteId,
        addBlotDatas: [{
          parent: 'scroll',
          blotName: 'hint',
          blotParams: {
            icon: 'https://cdn.jsdelivr.net/npm/emoji-datasource-apple/img/apple/64/1f447.png',
            delta: {
              ops: [
                {
                  insert: text
                }
              ]
            }
          },
          insertAfter: 'last'
        }]
      }

    });
    return body;
  }

  static async notesAddStep({ noteId, workspaceId, text = '', attachId = '' }) {
    const { body } = await this.requestSync({
      action: 'notes:addBlots',
      body: {
        workspaceId,
        globalId: noteId,
        addBlotDatas: [{
          parent: 'scroll',
          blotName: 'step',
          blotParams: {
            collapsed: false,
            delta: {
              ops: [
                {
                  insert: text
                }
              ]
            },
            items: [
              {
                type: 'image',
                initParams: {
                  attachmentGlobalId: attachId
                }
              }
            ]
          },
          insertAfter: 'last'
        }]
      }
    });
    return body;
  }

  static async notesAddFile({ noteId, type, workspaceId, attachId, insertAfter = 'last' }) {
    const { body } = await this.requestSync({
      action: 'notes:addBlots',
      body: {
        workspaceId,
        globalId: noteId,
        addBlotDatas: [{
          parent: 'scroll',
          blotName: type,
          blotParams: {
            attachmentGlobalId: attachId
          },
          insertAfter
        }]
      }

    });
    return body;
  }

  static async notesCreate({ noteId, workspaceId, parentId, pageUrl, attachements, title, type = 'note', role = 'note' }) {
    const { body } = await this.requestSync({
      action: 'notes:update',
      body: {
        workspaceId,
        store: {
          notes: [{
            global_id: noteId,
            parent_id: parentId,
            type,
            role,
            url: pageUrl,
            title,
            attachements: attachements.map(({ attachId, type, attachName }) => ({
              global_id: attachId,
              type,
              in_list: 0,
              tempname: attachName
            }))
          }]
        }
      }
    });
    return body;
  }

  /** @description notesShare шаринг заметки.
   * @param {array| string} global_id массив заметок/папок, либо строка с global_id, для которой надо отменить шаринг.
   * @param {string} workspaceId Параметр workspaceId может быть опущен, тогда работа будет вестись в воркспейсе, который является воркспейсом
   * по умолчанию
   * для текущего пользователя.
   * @return {object} body
   */

  // eslint-disable-next-line camelcase,default-param-last
  static async notesShare(global_id = [], workspaceId) {
    const { body } = await this.requestSync({
      action: 'notes:share',
      // eslint-disable-next-line camelcase
      body: { global_id, workspaceId }
    });

    return body;
  }

  /** @description notesUnShare отмена шаринга.
   * @param {array| string} global_id массив заметок/папок, либо строка с global_id, для которой надо отменить шаринг.
   * @param {string} workspaceId Параметр workspaceId может быть опущен, тогда работа будет вестись в воркспейсе, который является воркспейсом
   * по умолчанию
   * для текущего пользователя.
   * @return {object} body
   */

  // eslint-disable-next-line camelcase,default-param-last
  static async notesUnShare(global_id = [], workspaceId) {
    const { body } = await this.requestSync({
      action: 'notes:unshare',
      // eslint-disable-next-line camelcase
      body: { global_id, workspaceId }
    });

    return body;
  }

  /** @description updateScreencast the nimbus server.
   * @param {string} idToken токен обновления.
   * @param {{title?,commentText?,url?}} data данные для обновления.
   */

  static async updateScreencast(idToken, data = {}) {
    const { body } = await this.requestSync('post', '', {
      action: 'screencasts:update',
      body: { idToken, screen: data }
    });

    return body;
  }

  /** @description updateScreenshots the nimbus server.
   * @param {string} idToken токен обновления.
   * @param {{title?,commentText?,url?,share?}} data данные для обновления.
   * @param {string} workspaceId Параметр workspaceId может быть опущен, тогда работа будет вестись в воркспейсе, который является
   * воркспейсом по умолчанию
   * для текущего пользователя.
   */

  // eslint-disable-next-line default-param-last
  static async updateScreenshots(idToken, data = {}, workspaceId) {
    const { body } = await this.requestSync('post', '', {
      action: 'screenshots:update',
      body: { idToken, workspaceId, screen: data }
    });

    return body;
  }

  /** @description createMultipartUpload the nimbus server.
   * @return {object} body
   */

  static async createMultipartUpload(
    size = 0,
    type = 'video/webm',
    name = 'video.webm'
  ) {
    const { body } = await this.requestSync({
      action: 'files:createMultipartUpload',
      body: { size, type, name }
    });

    return body;
  }

  /** @description completeMultipartUpload the nimbus server.
   * @return {object} body
   */

  static async completeMultipartUpload(id, parts, tempFileName) {
    const { body } = await this.requestSync({
      action: 'files:completeMultipartUpload',
      body: { id, parts, tempFileName }
    });

    return body;
  }

  /** @description multipartUpload the nimbus server.
   * @return {object} body
   */

  // eslint-disable-next-line default-param-last
  static async multipartUpload(url = '', file) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest();

      xhr.open('put', url);
      xhr.onload = function onLoad() {
        const etag = xhr.getResponseHeader('etag').replaceAll('"', '');

        resolve(etag);
      };

      xhr.onerror = function onError(error) {
        reject(error);
      };

      xhr.send(file);
    });
  }

  static async authState() {
    const { errorCode, body } = await this.requestSync({
      action: 'user:authstate'
    });

    if (body) {
      const { authorized } = body;

      if (errorCode === 0 && authorized) {
        return true;
      }
    }

    LS.setItem('nimbusSessionId', null);
    return false;
  }

  static async auth(login, password) {
    return this.requestApi('auth', { login, password });
  }

  static async logout() {
    await this.requestSync({ action: 'user:logout' });

    LS.removeItem('nimbusSessionId');
  }

  static async register(params) {
    return this.requestApi('register', params);
  }

  static async challenge(state, answer) {
    return this.requestApi('challenge', { state, answer });
  }

  static async remindPassword(email) {
    return this.requestSync({ action: 'remind_password', email });
  }

  // eslint-disable-next-line camelcase,consistent-return
  static async saveScreencast({ blob, size, type, name, duration, comment = '', title = '' }, progress) {
    const { upload } = await this.createMultipartUpload(size, type, name);
    const { id } = upload;
    const { tempFileName } = upload;
    const { partSize } = upload;
    const { partsUrls } = upload;

    const parts = [];

    const message = percent => {
      if (progress && partSize < size) {
        progress(percent);
      }
    };

    for (let partNumber = 0, nextPartNumber = 1; partNumber < partsUrls.length; partNumber++, nextPartNumber++) {
      const file = blob.slice(partSize * partNumber, partSize * nextPartNumber);

      if (!partNumber) {
        message(1);
      }

      try {
        // eslint-disable-next-line no-await-in-loop
        const etag = await this.multipartUpload(partsUrls[partNumber], file);

        message(Math.ceil(nextPartNumber / partsUrls.length * 100));
        parts.push({ partNumber: nextPartNumber, etag });
      } catch (e) {
        console.error(e);
      }
    }
    const { file } = await this.completeMultipartUpload(id, parts, tempFileName);
    const { url = null } = await nscExt.pageInfo();
    const share = await LS.getItem('nimbusShare');
    // eslint-disable-next-line camelcase
    const parent_id = await LS.getItem('nimbusFolderSelect');
    const workspaceId = await LS.getItem('nimbusWorkspaceSelect');
    const { errorCode, body } = await this.requestSync({
      action: 'screencasts:save',
      features: {
        noteEditor: true
      },
      body: {
        workspaceId,
        screen: {
          title,
          commentText: comment,
          tempname: file.name,
          // eslint-disable-next-line camelcase
          parent_id,
          url,
          duration
        },
        share
      }
    });

    if (errorCode === -20) {
      // notificationReachedLimit
    } else if (errorCode === 0) {
      return body;
    } else {
      // notificationWrong
    }
  }

  // eslint-disable-next-line consistent-return,camelcase
  static async saveScreenshot({ blob, name, comment = '', title = '' }) {
    const { files } = await this.fileUpload({ blob, name, type: 'screens' });
    const { url = null } = await nscExt.pageInfo();
    const share = await LS.getItem('nimbusShare');
    // eslint-disable-next-line camelcase
    const parent_id = await LS.getItem('nimbusFolderSelect');
    const workspaceId = await LS.getItem('nimbusWorkspaceSelect');
    const { errorCode, body } = await this.requestSync({
      action: 'screenshots:save',
      features: {
        noteEditor: true
      },
      body: {
        workspaceId,
        screen: {
          commentText: comment,
          title,
          tempname: files.screens,
          // eslint-disable-next-line camelcase
          parent_id,
          url
        },
        share
      }
    });

    if (errorCode === -20) {
      // limit
    } else if (errorCode === 0) {
      return body;
    }
  }

  /** @description Loading info of user from the nimbus server.
   * @return {object} info user
   */

  static async getInfo() {
    return this.requestSync({
      action: 'user:info'
    });
  }

  /** @description Set default Select Org/Work/Note.
   * @param {object} items data array.
   * @param {string} idKey id in data key.
   * @param {string} storeKey localStorage key.
   * @return {object} default item
   */

  static async setDefaultSelect(items, idKey, storeKey) {
    if (!items || !items.length) return null;

    const saveStoreKey = await LS.getItem(storeKey);

    const search = items.find(
      item => item[idKey] === saveStoreKey
    );

    if (search) {
      return search;
    }

    LS.setItem(storeKey, items[0][idKey]);
    return items[0];
  }

  /** @description Loading organizations from the nimbus server.
   * @return {object} default organization
   */

  static async getPersonalOrganizations(userId) {
    const { errorCode, body } = await this.requestSync({
      action: 'orgs:info',
      globalId: `u${userId.toString(36)}`
    });

    if (errorCode !== 0) {
      console.error('getOrganizations', errorCode, body);
      return [];
    }

    const { org } = body;

    return org;
  }

  /** @description Loading organizations from the nimbus server.
   * @return {object} default organization
   */

  static async getOrganizations() {
    return this.requestSync({
      action: 'orgs:getAll',
      features: { privateOrgs: true }
    });
  }

  /** @description Loading workspaces from the nimbus server.
   * @return {object} default workspace
   */

  static async getWorkspaces(params = {}) {
    const orgSelect = await this.orgSelect();

    params = { orgId: orgSelect, ...params };

    return this.requestSync({
      action: 'notes:getWorkspaces',
      body: params
    });
  }

  /** @description Loading notes from the nimbus server.
   * @return {object} default note
   */

  static async getNotes(params = {}) {
    const workSelect = await this.workSelect();

    params = { workspaceId: workSelect, ...params };

    return this.requestSync({
      action: 'notes:getFolders',
      body: params
    });
  }
}
