import nscCore from '@/lib/core';
import LS from '@/lib/LocalStorage';
import nscExt from '@/lib/extension';
import ActionFragment from '@/content/ActionFragment';
import ContentPopup from '@/content/ContentPopup';
import ContentVideoCamera from '@/content/ContentVideoCamera';

export default class ContentVideoPanel {
  static canvas = {
    common: {},
    background: {
      canvas: null,
      ctx: null
    },
    animate: {
      canvas: null,
      ctx: null
    },
    current: {
      canvas: null,
      ctx: null
    }
  };

  static isLoadHtml = false;
  static isPanelMove = false;
  static panelMovePosition = {
    x: 0,
    y: 0
  };

  static isDraw = false;
  static requestAnimationId = null;
  static colorTools = {
    drawPen: '#00FF00',
    drawArrow: '#FF0000',
    drawSquare: '#0000FF',
    drawBlur: '#FFFFFF'
  };

  static notif = {
    // eslint-disable-next-line max-len,vue/max-len
    red: 'data:image/png;base64,',
    // eslint-disable-next-line max-len,vue/max-len
    blue: 'data:image/png;base64,',
    // eslint-disable-next-line max-len,vue/max-len
    green: 'data:image/png;base64,'
  };

  static currentTools = 'drawCursorDefault';

  static videoDrawingToolsDeleteInterval = null;

  static saveMemory = [];
  static memory = [];
  static startPoint = {};

  static historySave() {
    this.memory.push({
      date: Date.now(),
      data: this.canvas.current.ctx.getImageData(0, 0, this.canvas.common.width, this.canvas.common.height),
      tools: this.currentTools
    });
  }

  static historyRemove(sec) {
    let i = this.memory.length;
    while (i--) {
      if (this.memory[i].date < Date.now() - (sec * 1000)) this.memory.splice(i, 1);
    }
  }

  static historySaveMemory() {
    this.saveMemory.concat(this.memory);
  }

  static historyRemoveAll() {
    this.memory = [];
    this.historyDraw();
  }

  static historyDraw() {
    if (this.canvas.current.ctx) {
      this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    }
    if (this.canvas.background.ctx) {
      this.canvas.background.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    }

    const canvas = document.createElement('canvas');
    if (this.canvas.common) {
      canvas.width = this.canvas.common.width;
      canvas.height = this.canvas.common.height;
    }

    for (let i = 0, len = this.memory.length; i < len; i++) {
      const { data } = this.memory[i];
      canvas.getContext('2d')
        .putImageData(data, 0, 0);
      this.canvas.background.ctx.drawImage(canvas, 0, 0);
    }
  }

  static clear(container) {
    if (!this.canvas.common.container) {
      return container;
    }
    this.eventOff();

    this.canvas.common.container.remove();
    this.canvas.background = {};
    this.canvas.animate = {};
    this.canvas.current = {};
    this.memory = [];

    return container;
  }

  static eventOn() {
    document.body.addEventListener('mousedown', this.drawStart);
    document.body.addEventListener('mousemove', this.drawMove);
    document.body.addEventListener('mouseup', this.drawEnd);
  }

  static eventOff() {
    document.body.style.cursor = 'auto';

    document.body.removeEventListener('mousedown', this.drawStart);
    document.body.removeEventListener('mousemove', this.drawMove);
    document.body.removeEventListener('mouseup', this.drawEnd);

    if (this.requestAnimationId) {
      window.cancelAnimationFrame(this.requestAnimationId);
      this.requestAnimationId = null;
    }
  }

  static async activeTools(tool) {
    // console.log('activeTools', tool);

    if (this.requestAnimationId) {
      window.cancelAnimationFrame(this.requestAnimationId);
      this.requestAnimationId = null;
    }

    const capitalize = s => s && s[0].toUpperCase() + s.slice(1);

    tool = `draw${capitalize(tool)}`;

    if (ActionFragment.blurVideoPanel) {
      ActionFragment.eventClear();
      ActionFragment.viewClear();
      ActionFragment.blurVideoPanel = false;
      this.canvas.common.container.classList.remove('events');
    }

    if (this[tool] !== undefined && tool !== 'drawClearAll' && tool !== 'drawSaveMemory') {
      if (tool === 'drawCursorDefault' || tool === 'drawCursorShadow' || tool === 'drawCursorRing') {
        document.body.style.userSelect = 'auto';
        if (this.canvas.common.container) {
          this.canvas.common.container.classList.add('events');
        }
      } else {
        document.body.style.userSelect = 'none';
        if (this.canvas.common.container) {
          this.canvas.common.container.classList.remove('events');
        }
      }

      if (tool === 'drawCursorShadow') {
        this.drawCursorShadow(this.startPoint, { x: 0, y: this.canvas.common.height });
      } else if (tool === 'drawBlur' || tool === 'drawBlurArea') {
        LS.setItem('videoBlurProCheckActivateBlur', true);

        const videoBlurProCheckActivateRecord = await LS.getItem('videoBlurProCheckActivateRecord');
        const isNimbusPremium = await LS.getItem('isNimbusPremium');
        let videoBlurProCheckActivateCount = await LS.getItem('videoBlurProCheckActivateCount');

        if (videoBlurProCheckActivateRecord && !isNimbusPremium) {
          videoBlurProCheckActivateCount = +videoBlurProCheckActivateCount + 1;
          LS.setItem('videoBlurProCheckActivateRecord', false);
          LS.setItem('videoBlurProCheckActivateCount', videoBlurProCheckActivateCount);
        }

        if (videoBlurProCheckActivateCount >= 3 && !isNimbusPremium) {
          this.currentTools = 'cursorDefault';
          document.body.style.userSelect = 'auto';
          this.canvas.common.container.classList.add('events');
          await ContentPopup.premiumOpen({ analytics: 'blur_video' });
          return;
        }

        if (tool === 'drawBlurArea') {
          const { state } = await nscExt.getScreencastStatus();
          if (state === 'recording') {
            const videoRecordIsGuide = await LS.getItem('videoRecordIsGuide');
            const videoRecordType = await LS.getItem('videoRecordType');
            const lastCreateTabId = await LS.getItem('lastCreateTabId');

            if (videoRecordType === 'tab' || videoRecordIsGuide) {
              nscCore.sendMessageSync({ operation: 'pauseScreencast' });
            } else {
              nscCore.syncSendTabMessage({ id: lastCreateTabId }, { operation: 'pauseScreencast' });
            }
          }
          this.canvas.common.container.classList.add('events');
          ActionFragment.blurVideoPanel = true;
          ActionFragment.init();
        } else {
          document.body.style.cursor = 'crosshair';
        }
      } else {
        document.body.style.cursor = 'auto';
      }

      this.currentTools = tool;
      this.editorChangeTools(this.currentTools, this.colorTools[this.currentTools]);
      this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    } else if (tool === 'drawClearAll') {
      this.drawClearAll();
    } else if (tool === 'drawSaveMemory') {
      this.drawSaveMemory();
    } else if (!tool) {
      this.currentTools = false;
    } else {
      console.error(`Tool ${tool} not found!`);
    }
  }

  static activeColor(color) {
    if (this.colorTools[this.currentTools]) {
      this.colorTools[this.currentTools] = color;
      this.editorChangeTools(this.currentTools, this.colorTools[this.currentTools]);
    } else {
      this.editorNotSetColor();
    }
  }

  static deleteDrawing(date) {
    if (this.memory.length) {
      this.historyRemove(date);
      this.historyDraw();
    }
  }

  static getPosition(e) {
    const a = this.canvas.current.canvas.getBoundingClientRect();
    return {
      x: e.clientX - a.left,
      y: e.clientY - a.top
    };
  }

  static drawIsDom(e) {
    if (!e) {
      return false;
    }

    const [videoEditor] = document.getElementsByClassName('nsc-video-editor');
    const style = window.getComputedStyle(videoEditor);

    return style.display === 'none'
      || e.target.closest('.nsc-panel-compact')
      || e.target.classList.contains('.nsc-panel-compact')
      || e.target.closest('.nsc-content-camera')
      || e.target.classList.contains('.nsc-content-camera');
  }

  static drawStart = e => {
    if (!this.drawIsDom(e)) {
      this.isDraw = true;
      this.startPoint = this.getPosition(e);

      // eslint-disable-next-line default-case
      switch (this.currentTools) {
        case 'drawCursorRing':
        case 'drawNotifGreen':
        case 'drawNotifRed':
        case 'drawNotifBlue':
          this[this.currentTools](this.startPoint, this.getPosition(e));
          break;
      }
    }
  };

  static drawMove = e => {
    if (!this.drawIsDom(e)) {
      if (this.isDraw && this.currentTools) {
        this[this.currentTools](this.startPoint, this.getPosition(e));
      }

      // eslint-disable-next-line default-case
      switch (this.currentTools) {
        case 'drawCursorShadow':
          this[this.currentTools](this.startPoint, this.getPosition(e));
          break;
      }
    }
  };

  static drawEnd = e => {
    if (!this.drawIsDom(e)) {
      this.isDraw = false;
      this.startPoint = {};
      // eslint-disable-next-line default-case
      switch (this.currentTools) {
        case 'drawPen':
        case 'drawSquare':
        case 'drawArrow':
        case 'drawNotifGreen':
        case 'drawNotifRed':
        case 'drawNotifBlue':
        case 'drawBlur':
        case 'drawBlurArea':
          this.historySave();
          this.canvas.background.ctx.drawImage(this.canvas.current.canvas, 0, 0);
          this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
          break;
      }
    }
  };

  static drawCursorDefault() {
  }

  static drawCursorNone() {
  }

  static drawCursorRing(start, end) {
    let ringCounter = 0;
    let ringRadius;

    // eslint-disable-next-line no-mixed-operators,no-return-assign
    const easeInCubic = (now, startValue, deltaValue, duration) => deltaValue * (now /= duration) * now * now + startValue;

    // eslint-disable-next-line no-mixed-operators,no-return-assign
    const easeOutCubic = (now, startValue, deltaValue, duration) => deltaValue * ((now = now / duration - 1) * now * now + 1) + startValue;

    const animate = () => {
      if (ringCounter > 200) {
        return;
      }

      this.requestAnimationId = window.requestAnimationFrame(animate);

      if (ringCounter < 100) {
        ringRadius = easeInCubic(ringCounter, 0, 20, 100);
      } else {
        ringRadius = easeOutCubic(ringCounter - 100, 20, -20, 100);
      }

      this.canvas.animate.ctx.lineWidth = 1;
      this.canvas.animate.ctx.strokeStyle = 'red';
      this.canvas.animate.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
      this.canvas.animate.ctx.beginPath();
      this.canvas.animate.ctx.arc(end.x, end.y, ringRadius, 0, Math.PI * 2);
      this.canvas.animate.ctx.stroke();
      ringCounter += 5;
    };

    this.requestAnimationId = window.requestAnimationFrame(animate);
  }

  static drawCursorShadow(start, end) {
    this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.globalAlpha = 0.6;
    this.canvas.current.ctx.fillStyle = '#000';
    this.canvas.current.ctx.fillRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.globalAlpha = 1;
    this.canvas.current.ctx.beginPath();
    this.canvas.current.ctx.globalCompositeOperation = 'destination-out';
    this.canvas.current.ctx.filter = 'blur(10px)';
    this.canvas.current.ctx.arc(end.x, end.y, 75, 0, 2 * Math.PI, true);
    this.canvas.current.ctx.fill();
    // this.canvas.current.ctx.beginPath();
    // this.canvas.current.ctx.globalAlpha = 0.8;
    this.canvas.current.ctx.globalCompositeOperation = 'source-over';
    this.canvas.current.ctx.filter = 'none';
    // this.canvas.current.ctx.arc(end.x, end.y, 2, 0, 2 * Math.PI, true);
    // this.canvas.current.ctx.fill();
    this.canvas.current.ctx.globalAlpha = 1;
  }

  static drawClear(start, end) {
    this.canvas.background.ctx.beginPath();
    this.canvas.background.ctx.globalCompositeOperation = 'destination-out';
    this.canvas.background.ctx.arc(end.x, end.y, 20, 0, Math.PI * 2, true);
    this.canvas.background.ctx.fill();
    this.canvas.background.ctx.globalCompositeOperation = 'source-over';
  }

  static drawSaveMemory() {
    this.historyRemoveAll();
  }

  static drawClearAll() {
    this.historyRemoveAll();
  }

  static drawNotif(start, end, name) {
    const image = new Image();
    const ratio = 2;
    image.src = this.notif[name];
    image.onload = () => {
      this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);

      this.canvas.current.ctx.drawImage(
        image,
        0,
        0,
        image.width,
        image.height,
        Math.round(end.x - (image.width / ratio / ratio)),
        Math.round(end.y - (image.height / ratio / ratio)),
        Math.round(image.width / ratio),
        Math.round(image.height / ratio)
      );
    };
  }

  static drawNotifGreen(start, end) {
    this.drawNotif(start, end, 'green');
  }

  static drawNotifRed(start, end) {
    this.drawNotif(start, end, 'red');
  }

  static drawNotifBlue(start, end) {
    this.drawNotif(start, end, 'blue');
  }

  static drawPen(start, end) {
    this.canvas.current.ctx.lineWidth = 3;
    this.canvas.current.ctx.strokeStyle = this.colorTools.drawPen;
    this.canvas.current.ctx.beginPath();
    this.canvas.current.ctx.moveTo(start.x, start.y);
    this.canvas.current.ctx.lineTo(end.x, end.y);
    this.canvas.current.ctx.stroke();
    this.startPoint = {
      x: end.x,
      y: end.y
    };
  }

  static drawArrow(start, end) {
    const dx = start.x - end.x;
    const dy = start.y - end.y;
    const angle = Math.atan2(dy, dx) + Math.PI;
    const headLength = 15;
    this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.lineWidth = 3;
    this.canvas.current.ctx.strokeStyle = this.colorTools.drawArrow;
    this.canvas.current.ctx.fillStyle = this.colorTools.drawArrow;
    this.canvas.current.ctx.beginPath();
    this.canvas.current.ctx.moveTo(start.x, start.y);
    this.canvas.current.ctx.lineTo(end.x, end.y);
    this.canvas.current.ctx.stroke();
    this.canvas.current.ctx.moveTo(end.x, end.y);
    // eslint-disable-next-line no-mixed-operators
    this.canvas.current.ctx.lineTo(end.x - headLength * Math.cos(angle - Math.PI / 7), end.y - headLength * Math.sin(angle - Math.PI / 7));
    // eslint-disable-next-line no-mixed-operators
    this.canvas.current.ctx.lineTo(end.x - headLength * Math.cos(angle + Math.PI / 7), end.y - headLength * Math.sin(angle + Math.PI / 7));
    this.canvas.current.ctx.lineTo(end.x, end.y);
    // eslint-disable-next-line no-mixed-operators
    this.canvas.current.ctx.lineTo(end.x - headLength * Math.cos(angle - Math.PI / 7), end.y - headLength * Math.sin(angle - Math.PI / 7));
    this.canvas.current.ctx.fill();
  }

  static drawSquare(start, end) {
    this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.beginPath();
    this.canvas.current.ctx.lineWidth = 3;
    this.canvas.current.ctx.strokeStyle = this.colorTools.drawSquare;
    this.canvas.current.ctx.strokeRect(start.x, start.y, end.x - start.x, end.y - start.y);
  }

  static drawBlur(start, end) {
    this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.globalAlpha = 0.97;
    this.canvas.current.ctx.filter = 'blur(5px)';
    this.canvas.current.ctx.fillStyle = this.colorTools.drawBlur;
    this.canvas.current.ctx.fillRect(start.x, start.y, end.x - start.x, end.y - start.y);
    this.canvas.current.ctx.globalAlpha = 1;
    this.canvas.current.ctx.filter = 'none';
  }

  static drawBlurArea({ w, h, x, y }) {
    this.canvas.current.ctx.clearRect(0, 0, this.canvas.common.width, this.canvas.common.height);
    this.canvas.current.ctx.globalAlpha = 0.97;
    this.canvas.current.ctx.filter = 'blur(5px)';
    this.canvas.current.ctx.fillStyle = this.colorTools.drawBlur;
    this.canvas.current.ctx.fillRect(x, y, w, h);
    this.canvas.current.ctx.globalAlpha = 1;
    this.canvas.current.ctx.filter = 'none';
  }

  static getElemAppend() {
    return document.fullscreenElement ? document.fullscreenElement : document.body;
  }

  static canvasCleanup() {
    [...document.querySelectorAll('.nsc-video-editor canvas')].forEach(canvas => {
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, canvas.width, canvas.height);
    });
  }

  static getScrollSize() {
    const {
      scrollHeight,
      offsetHeight,
      clientHeight,
      scrollWidth,
      offsetWidth,
      clientWidth
    } = document.documentElement;

    return {
      height: Math.max(scrollHeight, offsetHeight, clientHeight),
      width: Math.max(scrollWidth, offsetWidth, clientWidth)
    };
  }

  static async init({ isShowContentVideoPanel, status, state }) {
    const videoDrawingToolsEnable = await LS.getItem('videoDrawingToolsEnable');
    const videoDrawingToolsDelete = await LS.getItem('videoDrawingToolsDelete');
    const videoAnimationCursor = await LS.getItem('videoAnimationCursor');

    const panelCompact = document.querySelector('.nsc-panel-compact');
    const videoEditor = document.querySelector('.nsc-video-editor');

    if (!panelCompact && !videoEditor) {
      await this.insertHtml();
    }

    if (videoDrawingToolsDelete) {
      const clearIntervalToolsDelete = isClear => {
        if (isClear) {
          clearInterval(this.videoDrawingToolsDeleteInterval);
          this.videoDrawingToolsDeleteInterval = null;
        }
      };

      clearIntervalToolsDelete(this.videoDrawingToolsDeleteInterval);

      this.videoDrawingToolsDeleteInterval = setInterval(async () => {
        try {
          const response = await nscExt.getScreencastStatus();
          clearIntervalToolsDelete(!response?.status && this.videoDrawingToolsDeleteInterval);
        } catch (error) {
          clearIntervalToolsDelete(this.videoDrawingToolsDeleteInterval);
        }
        this.deleteDrawing(videoDrawingToolsDelete);
      }, 500);
    }

    if (videoDrawingToolsEnable && (status || isShowContentVideoPanel)) {
      await this.showEditor();
      const buttonPlay = document.getElementById('nsc_panel_button_play');
      const buttonPause = document.getElementById('nsc_panel_button_pause');
      const buttonStop = document.getElementById('nsc_panel_button_stop');

      if (status) {
        buttonStop.classList.remove('nsc-hide');
      } else {
        buttonStop.classList.add('nsc-hide');
      }

      if (state === 'recording') {
        buttonPlay.classList.add('nsc-hide');
        buttonPause.classList.remove('nsc-hide');
      } else {
        buttonPlay.classList.remove('nsc-hide');
        buttonPause.classList.add('nsc-hide');
      }
    } else if (status && videoAnimationCursor) {
      await this.showEditor(true);
    } else {
      await this.hideEditor();
    }

    if (status) {
      await this.activeTools(videoAnimationCursor ? 'cursorRing' : 'cursorDefault');
    } else {
      this.canvasCleanup();
      await this.activeTools(videoAnimationCursor ? 'cursorRing' : 'cursorDefault');
    }
  }

  static async insertHtml() {
    const html = await fetch(chrome.runtime.getURL('assets/template/panel-video-compact.html')).then(res => res.text());
    const scrollSize = this.getScrollSize();

    const setTemplate = () => {
      const container = document.createElement('div');
      container.classList.add('nsc-video-editor', 'nsc-hide');
      this.getElemAppend().insertAdjacentHTML('beforeend', html);
      this.getElemAppend().appendChild(container);
      // console.log('setTemplate');
    };

    document.addEventListener('fullscreenchange', setTemplate);
    setTemplate();

    this.canvas.common.container = document.querySelector('.nsc-video-editor');
    this.canvas.common.container.style.width = `${scrollSize.width}px`;
    this.canvas.common.container.style.height = `${scrollSize.height}px`;

    this.canvas.common.width = scrollSize.width;
    this.canvas.common.height = scrollSize.height;

    this.canvas.background.canvas = document.createElement('canvas');
    this.canvas.background.canvas.width = this.canvas.common.width;
    this.canvas.background.canvas.height = this.canvas.common.height;
    this.canvas.background.canvas.style.position = 'absolute';
    this.canvas.background.canvas.style.top = 0;
    this.canvas.background.canvas.style.left = 0;
    this.canvas.background.canvas.style.zIndex = 0;

    this.canvas.animate.canvas = document.createElement('canvas');
    this.canvas.animate.canvas.width = this.canvas.common.width;
    this.canvas.animate.canvas.height = this.canvas.common.height;
    this.canvas.animate.canvas.style.position = 'absolute';
    this.canvas.animate.canvas.style.top = 0;
    this.canvas.animate.canvas.style.left = 0;
    this.canvas.animate.canvas.style.zIndex = 1;

    this.canvas.current.canvas = document.createElement('canvas');
    this.canvas.current.canvas.width = this.canvas.common.width;
    this.canvas.current.canvas.height = this.canvas.common.height;
    this.canvas.current.canvas.style.position = 'absolute';
    this.canvas.current.canvas.style.top = 0;
    this.canvas.current.canvas.style.left = 0;
    this.canvas.current.canvas.style.zIndex = 2;

    this.canvas.background.ctx = this.canvas.background.canvas.getContext('2d');
    this.canvas.animate.ctx = this.canvas.animate.canvas.getContext('2d');
    this.canvas.current.ctx = this.canvas.current.canvas.getContext('2d');

    this.canvas.common.container.appendChild(this.canvas.background.canvas);
    this.canvas.common.container.appendChild(this.canvas.animate.canvas);
    this.canvas.common.container.appendChild(this.canvas.current.canvas);
    this.canvas.common.container.classList.add('events');

    const editorActiveTools = document.querySelectorAll('*[data-event^=nimbus-editor-active]');

    editorActiveTools.forEach(button => {
      button.addEventListener('click', e => {
        const dataEvent = e.currentTarget.getAttribute('data-event');
        const dataEventParam = e.currentTarget.getAttribute('data-event-param');

        if (dataEvent === 'nimbus-editor-active-tools') {
          this.activeTools(dataEventParam);
          LS.setItem('videoEditorTools', dataEventParam);
        } else {
          this.activeColor(dataEventParam);
        }
      });
    });

    const compactTrigger = document.querySelectorAll('.nsc-panel-compact-trigger');

    compactTrigger.forEach(elem => {
      elem.addEventListener('click', e => {
        const currentButton = e.currentTarget.closest('.nsc-panel-compact-button');

        Array.from(document.querySelectorAll('.nsc-panel-compact-button'))
          .filter(item => item !== currentButton)
          .forEach(button => {
            button.classList.remove('opened');
          });

        currentButton.classList.toggle('opened');
      });
    });

    const i18nElement = document.querySelectorAll('.nsc-panel-compact  *[data-i18n]');

    i18nElement.forEach(elem => {
      elem.addEventListener('mouseenter', ({ target }) => {
        const panelCompactTooltipLayout = document.querySelector('.nsc-panel-compact-tooltip-layout');
        const panelCompact = document.querySelector('.nsc-panel-compact');
        const text = target.getAttribute('data-i18n');
        const message = chrome.i18n.getMessage(text);

        if (panelCompactTooltipLayout) {
          panelCompactTooltipLayout.innerText = message || text;
        }

        if (panelCompact) {
          panelCompact.classList.add('nsc-tooltip');
        }
      });

      elem.addEventListener('mouseleave', () => {
        const panelCompact = document.querySelector('.nsc-panel-compact');

        if (panelCompact) {
          panelCompact.classList.remove('nsc-tooltip');
        }
      });
    });

    const buttonPlay = document.getElementById('nsc_panel_button_play');
    const buttonPause = document.getElementById('nsc_panel_button_pause');
    const buttonStop = document.getElementById('nsc_panel_button_stop');
    const buttonCamera = document.getElementById('nimbus_web_camera_toggle');

    buttonCamera.addEventListener('click', async () => {
      await ContentVideoCamera.toggleCamera();
    });

    buttonPlay.addEventListener('click', async () => {
      const { status } = await nscExt.getScreencastStatus();
      if (status) {
        await nscCore.sendMessage({ operation: 'pauseScreencastBG' });
      } else {
        await nscCore.sendMessage({ operation: 'startScreencast' });
      }
    });

    buttonPause.addEventListener('click', async () => {
      await nscCore.sendMessage({ operation: 'pauseScreencastBG' });
    });

    buttonStop.addEventListener('click', async () => {
      await nscCore.sendMessage({ operation: 'stopScreencastBG' });
    });

    const compactHideButton = document.querySelector('.nsc-panel-compact-hide-button');

    compactHideButton.addEventListener('click', async () => {
      const panelCompact = document.querySelector('.nsc-panel-compact');

      if (panelCompact) {
        panelCompact.classList.add('minimized');
        LS.setItem('videoDrawingToolsMinimized', true);
      }
    });

    const compactMaximumButton = document.querySelector('.nsc-panel-compact-maximum-button');

    compactMaximumButton.addEventListener('click', async () => {
      const panelCompact = document.querySelector('.nsc-panel-compact');

      if (panelCompact) {
        panelCompact.classList.remove('minimized');
        LS.setItem('videoDrawingToolsMinimized', false);
      }
    });

    const compactToggleButton = document.querySelector('.nsc-panel-compact-toggle-button');

    compactToggleButton.addEventListener('click', async () => {
      const { status } = await nscExt.getScreencastStatus();

      if (status) {
        await this.activeTools('cursorRing');
        LS.setItem('videoDrawingToolsEnable', false);
        await this.hideEditor();
      } else {
        await this.activeTools('clearAll');
        await this.hideEditor();
      }
    });

    document.addEventListener('keydown', async e => {
      if (e.altKey && e.key !== 'Alt') {
        let tools = 'cursorDefault';

        const panelCompact = document.querySelector('.nsc-panel-compact');

        // eslint-disable-next-line default-case
        switch (e.code) {
          case 'KeyV': { // v
            let videoDrawingToolsEnable = await LS.getItem('videoDrawingToolsEnable');

            if (document.querySelector('.nsc-panel-compact')
              .classList
              .contains('nsc-hide')) {
              panelCompact.classList.remove('nsc-hide');
              tools = await LS.getItem('videoEditorTools');
              videoDrawingToolsEnable = true;
            } else {
              panelCompact.classList.add('nsc-hide');
              tools = 'cursorDefault';
              videoDrawingToolsEnable = false;
            }

            LS.setItem('videoDrawingToolsEnable', videoDrawingToolsEnable);
            return;
          }
          case 'KeyS':
            tools = 'cursorDefault';
            break;
          case 'KeyG':
            tools = 'cursorShadow';
            break;
          case 'KeyL':
            tools = 'cursorRing';
            break;
          case 'KeyP':
            tools = 'pen';
            break;
          case 'KeyA':
            tools = 'arrow';
            break;
          case 'KeyR':
            tools = 'square';
            break;
          case 'KeyM':
            tools = 'notifRed';
            break;
          case 'KeyQ':
            tools = 'notifBlue';
            break;
          case 'KeyC':
            tools = 'notifGreen';
            break;
          case 'KeyU':
            tools = 'clearAll';
            break;
          case 'KeyB':
            tools = 'blur';
            break;
        }

        await this.activeTools(tools);
        LS.setItem('videoEditorTools', tools);
      }
    });

    document.querySelector('.nsc-panel-compact').addEventListener('mousedown', e => {
      if (e.buttons === 1) {
        const panelCompact = document.querySelector('.nsc-panel-compact');
        this.isPanelMove = true;
        this.panelMovePosition = { x: e.clientX - (parseInt(panelCompact.style.left, 10) || 0), y: e.clientY + (parseInt(panelCompact.style.bottom, 10) || 0) };
      }
    });

    document.addEventListener('mousemove', e => {
      if (this.isPanelMove) {
        const panelCompact = document.querySelector('.nsc-panel-compact');
        const { width, height } = panelCompact.getBoundingClientRect();
        const { x, y } = this.panelMovePosition;
        const left = e.clientX - x;
        const bottom = y - e.clientY;
        if (left <= 5 || left > window.innerWidth - width - 5 || bottom <= 5 || bottom > window.innerHeight - height - 5) {
          return;
        }
        panelCompact.style.left = `${left}px`;
        panelCompact.style.bottom = `${bottom}px`;
      }
    });

    document.querySelector('.nsc-panel-compact').addEventListener('mouseup', () => {
      this.isPanelMove = false;
    });

    document.addEventListener('mouseup', () => {
      this.isPanelMove = false;
    });

    const videoDrawingToolsMinimized = await LS.getItem('videoDrawingToolsMinimized');
    const panelCompact = document.querySelector('.nsc-panel-compact');

    if (videoDrawingToolsMinimized && panelCompact) {
      panelCompact.classList.add('minimized');
    }

    this.eventOn();
  }

  static showEditor(onlyPanel) {
    // console.log('showEditor');

    const panelCompact = document.querySelector('.nsc-panel-compact');
    const videoEditor = document.querySelector('.nsc-video-editor');

    if (panelCompact && videoEditor) {
      videoEditor.classList.remove('nsc-hide');
      if (!onlyPanel) {
        panelCompact.classList.remove('nsc-hide');
      }
    }
  }

  static hideEditor(onlyPanel) {
    // console.log('hideEditor');
    const panelCompact = document.querySelector('.nsc-panel-compact');
    const videoEditor = document.querySelector('.nsc-video-editor');

    if (panelCompact && videoEditor) {
      panelCompact.classList.add('nsc-hide');
      if (!onlyPanel) {
        videoEditor.classList.add('nsc-hide');
      }
    }
  }

  static editorChangeTools(tools, color) {
    // console.log('editorChangeTools', tools, color);
    if (tools) {
      tools = tools.replace('draw', '').substring(0, 1).toLowerCase() + tools.slice(5);

      const dropdownIcon = document.querySelector(`.nsc-panel-compact-dropdown-icon[data-event-param=${tools}]`);
      if (dropdownIcon) {
        const panelCompactButton = dropdownIcon.closest('.nsc-panel-compact-button');
        const panelCompactText = panelCompactButton.querySelector('.nsc-panel-compact-text');

        panelCompactButton.classList.remove('opened');

        panelCompactText.setAttribute('data-event', dropdownIcon.getAttribute('data-event'));
        panelCompactText.setAttribute('data-event-param', dropdownIcon.getAttribute('data-event-param'));
        panelCompactText.setAttribute('data-i18n', dropdownIcon.getAttribute('data-i18n'));

        while (panelCompactText.firstChild) {
          panelCompactText.removeChild(panelCompactText.lastChild);
        }

        panelCompactText.appendChild(dropdownIcon.querySelector('.nsc-icon')
          .cloneNode(true));
      }

      const panelCompactButtonTools = document.querySelector(`.nsc-panel-compact-button[data-event-param=${tools}]`);
      const panelCompactTextTools = document.querySelector(`.nsc-panel-compact-text[data-event-param=${tools}]`);

      if (panelCompactButtonTools || panelCompactTextTools) {
        const panelCompactButton = document.querySelectorAll('.nsc-panel-compact-button');

        panelCompactButton.forEach(button => {
          button.classList.remove('active');

          if (panelCompactButtonTools) {
            document.querySelector(`.nsc-panel-compact-button[data-event-param=${tools}]`).classList.add('active');
          } else {
            panelCompactTextTools
              .closest('.nsc-panel-compact-button')
              .classList
              .add('active');
          }
        });
      }
    }
    if (color) {
      const panelButtonColors = document.getElementById('nsc_panel_button_colors');
      panelButtonColors.style.backgroundColor = color;
      panelButtonColors.closest('.nsc-panel-compact-button')
        .classList
        .remove('opened');
    }
  }

  static editorNotSetColor() {
    document.getElementById('nsc_panel_button_colors')
      .closest('.nsc-panel-compact-button')
      .classList
      .remove('opened');
  }
}
